<template>
  <div class="w-header clear">
    <!--左侧部分-->
    <div class="left">
      <van-icon @click="goback" name="arrow-left" class="arrow-left"></van-icon>

    </div>
    <!--中部部分-->
    <div class="middle">
      <span>
        {{title}}
      </span>
    </div>
    <!--右侧部分-->
    <!--<div class="right">-->
      <!--<span>{{2}}</span>-->
    <!--</div>-->
  </div>
</template>

<script>
  export default {
    name: "logicheader",
    data(){
      return{

      }
    },
    props:{
      title:{
        type:String
      }
    },
    methods:{
      goback(){
        this.$router.go(-1)
      }
    },
  }
</script>

<style scoped lang="less">
  /*头部css样式*/
  .w-header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    width: 100%;
    line-height: 1rem;
    height: 1rem;
    font-size: 0.4rem;
    background: #3190e8;
    padding: 0 0.2rem;
    text-align: center;
    /*左侧样式*/
    .left{
      .arrow-left{
        color: #F5F5F5;
        font-size: 0.4rem;
        padding-top: 0.3rem;
      }
    }
    /*中部样式*/
    .middle{
      span{
        color: white;
        font-weight: bold;
        font-size: 0.4rem;
      }
    }
    /*右侧样式*/
    /*.right{*/
      /*span{*/
        /*display: inline-block;*/
      /*}*/

    /*}*/


  }
</style>



