<template>
    <div class="mine">
      <my-header></my-header>
      <!--上部图片-->
      <div class="img">
        <img src="" alt="">
      </div>
      <!--下部评分月售，评论-->
      <div class="bottom">
        <p>好吃而不腻</p>
        <h4>香辣片2</h4>
        <!--评分，星星-->
        <div class="point">
          <span>评分</span>
          <van-icon name="star"  class="van"/>
          <van-icon name="star" class="van"/>
          <van-icon name="star" class="van"/>
          <van-icon name="star" class="van"/>
          <van-icon name="star" class="van"/>
          <span class="span1">4.4</span>
        </div>
       <!--月售，售价-->
        <div class="point">
          <span>月售 406单</span>
          <span class="span2">售价 ￥20 起</span>
        </div>
        <!--评论数，好评率-->
        <div class="point">
          <span>评论数 40</span>
          <span>好评率 11%</span>
        </div>
        <!--底部购物车-->
        <div class="bot clearfix">
          <!--左边-->
          <div class="left">
            <h4>图片</h4>
            <p>￥0.00</p>
            <span>配送费￥5</span>
          </div>
         <!--右边-->
          <p class="right">还差￥20起送</p>
        </div>
      </div>
    </div>
</template>

<script>
  import MyHeader from "../mine-2/myHeader";
    export default {
        name: "ask",
      components:{MyHeader}
    }
</script>

<style scoped lang="less">
.mine{
  background-color: white;
  .img{
    margin-top: 1rem;
    background-color: lightgreen;
    width: 100%;
    height: 7.2rem;
  }
  .bottom{
    p{
      padding: .2rem;
      font-size: .3rem;
      color: #666;
    }
    h4{
     padding: .12rem .2rem;
    }
    .point{
      padding: .05rem .2rem .1rem;
      font-size: 0;
    }
    span{
      color: #666;
      font-size: .3rem;
      padding-right: .12rem;
    }
    .van{
      color: #ff9a0d;
      font-size: .01rem;
     vertical-align: -.1rem;
    }
    .span1{
      color: #f60;
      font-size: .1rem;
      padding-left: .1rem;
    }
    .span2{
      color: #f60;
      padding-left: .1rem;
    }
  }
  .bot{
    background-color: #333;
    margin-top: 1.2rem;
    position: relative;
    h4{
      width: 1.2rem;
      height: 1.2rem;
      background-color: blue;
      margin: -.2rem .3rem;
      border-radius: 50%;
    }
    p{
      padding-left: 1.7rem;
      margin: -1rem 0 .3rem;
      font-size: .4rem;
      color: white;
      font-weight: 700;
    }
    span{
      color: white;
      position: absolute;
      left: 1.7rem;
      top: .7rem;
    }
    .right{
      width: 2.6rem;
      background-color: #535356;
      padding: .36rem 0 .36rem .28rem;
      margin:0;
      font-size:.35rem;
    }
  }
}
</style>



