<template>
    <div class="mine">
      <my-header></my-header>
      <div class="top">
        <span>联系人</span>
        <input type="text" placeholder="你的名字" v-model="input1">
        <div class="choose">
          <van-icon name="checked" class="van"/>
          <span>先生</span>
          <van-icon name="checked" class="van1"/>
          <span>女士</span>
        </div>
          <span>联系电话</span>
          <input type="text" placeholder="你的手机号" v-model="input2">
          <span class="span1">+</span>
        <div class="send">
          <span>送餐地址</span>
          <input type="text" placeholder="小区/写字楼/学校等" @click="jump" v-model="input3">
        </div>
        <input type="text" placeholder="详细地址（如门牌号等）" class="xinxi" v-model="input4">
        <div class="tag">
          <span>标签</span>
          <input type="text" placeholder="无/家/学校/公司" v-model="input5">
        </div>
      </div>
      <button class="btn1" @click="kuang">确定</button>
      <!--弹出的框动画-->
      <transition enter-active-class="animated bounceIn" leave-active-class="opacity 0">
        <div class="kuang" v-if="show">
          <van-icon name="warning-o"  class="van1"/>
          <p>IOS用户请前往AppStore下载</p>
          <button @click="confirm">确认</button>
        </div>
      </transition>
    </div>
</template>

<script>
  import Vue from "vue"
  import MyHeader from "../mine-2/myHeader";
    export default {
        name: "newaddress",
      components:{MyHeader},
      data(){
          return{
            show:false,
            data:"",
            input1:"",
            input2:"",
            input3:"",
            input4:"",
            input5:"",
            username:"",
          }
      },
      methods:{
        kuang(){
          this.show=true;
          Vue.axios.get(`/v1/users/${this.username.user_id}/addresses`).then((result)=>{
            console.log(result.data);
          }).catch((error)=>{
            console.log(error);
          })
        },
        confirm(){
          this.show=false;
        },
        jump(){
          this.$router.push({path:'/searchaddress'})
        }
      },
      created(){
        this.data = JSON.parse(localStorage.getItem('address'));
        this.input3=this.data.name;
        this.username = JSON.parse(localStorage.getItem('localdata'));
      }
    }
</script>

<style scoped lang="less">
.top{
  background-color: white;
  margin-top: 1rem;
  span{
    display: inline-block;
    width:26%;
    padding: .3rem;
  }
  input{
    width:58%;
    padding: .3rem .4rem;
  }
  .choose{
    padding: .4rem 0;
    border: .01rem solid #f5f5f5;
    .van{
      color: #4cd964;
      margin-left: 2.4rem;
    }
    .van1{
      color: #ccc;
    }
    span{
      padding: 0;
    }
  }
  .span1{
    color: #3190e8;
    font-size: .7rem;
    padding: .1rem 0;
    width: 1rem;
  }
  .xinxi{
    margin-left: 2rem;
  }
}
.btn1{
  width: 94%;
  margin-left: 3%;
  background-color: #4cd964;
  font-size: .3rem;
  color: #fff;
  text-align: center;
  line-height: .8rem;
  border-radius: .1rem;
  margin-top: .4rem;
}
.kuang{
  width: 74%;
  background-color: #fff;
  text-align: center;
  position: absolute;
  left: 13%;
  top: 3rem;
  border-radius:.25rem .25rem 0 0 ;
  .van1{
    padding-top: .2rem;
    font-size: 1.5rem;
    color: #f8cb86;
  }
  p{
    font-size: .3rem;
    color: #333;
    line-height: .4rem;
    margin-top: .3rem;
    padding-bottom: .3rem;
  }
  button{
    font-size: .4rem;
    color: #fff;
    font-weight: 700;
    margin-top: .4rem;
    background-color: #4cd964;
    width: 100%;
    line-height: .9rem;
    border-radius: 0 0 .25rem .25rem;
  }
}
</style>



