<template>
    <div class="mine"  @mousewheel.prevent>
      <my-header></my-header>
      <div class="bottom">
        <div class="foot clearfix" v-for="(value,index) in array1" :key="index">
          <div class="left">
            <span>￥</span>
            <span>{{value.amount}}</span>
            <p>满 {{value.sum_condition}}元可用</p>
          </div>
          <div class="right">
            <h4>{{value.name}}</h4>
            <p>{{value.end_date}}到期</p>
            <p>限收货手机号为 {{value.phone}}</p>
            <img src="https://place-hold.it/60x60" alt="">
          </div>
        </div>
      </div>

    </div>
</template>

<script>
  import  Vue from "vue"
  import MyHeader from "./myHeader";
    export default {
        name: "redpackgehistory",
      components: {MyHeader},
      data(){
          return{
            array1:[],
            userid:"",
          }
      },
      mounted(){
          Vue.axios.get(`promotion/v2/users/${this.userid.user_id}/expired_hongbaos?limit=20&ffset=0`
          ).then((result)=>{
            console.log(result.data);
            this.array1=result.data;
          })
      },
      created(){
        this.userid = this.$store.state.userStatus
      }
    }
</script>

<style scoped lang="less">
  .bottom{
    margin-top: 1.2rem;
    width: 94%;
    display: inline-block;
    background-color: #f2f2f2;
    margin-left: 3%;
    .foot{
      background: #FFE url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAICAYAAAD5nd/tAAAAAXNSR0IArs4c6QAAAHtJREFUKBVjvHv37n8GKgImKpoFNmoYGcjIyAgK6+VA2o6FhUWInZ1dlomJKQDIP40cbIzERApQ01+g5jAFBYV1yJpB7P///zPev39/IpDOBfGJDcNGbIaBDAC5XFFRsQBIHwDxCRoIVPidmZl5CkgxLgBU8w/owl6QPABvmyDiDQUF7gAAAABJRU5ErkJggg==) repeat-x;
      background-size: .2rem .1rem;
      padding: .6rem .2rem .4rem .2rem;
      width: 100%;
      font-size: 0;
      margin-top: .3rem;
      .left{
        width: 28%;
        padding-right: .2rem;
        border-right: .5px dashed #ccc;
        span{
          font-size: .4rem;
          color: #ccc;
          font-weight: 700;
        }
        span:nth-child(2){
          font-size: .7rem;
          font-weight: 400;
        }
        p{
          font-size: .2rem;
          color: #999;
          padding-top:.1rem;
        }
      }
      .right{
        width: 70%;
        padding-left: .2rem;
        h4{
          font-size: .33rem;
          font-weight: 400;
          color: #666;
        }
        p{
          color: #999;
          font-size: .22rem;
          padding-top:.1rem ;
        }
        img{
          padding-left: 3.5rem;
          margin-top: -1.4rem;
          color: #ff5340;
          font-size: .38rem;
        }
      }
    }
  }
</style>



