<template>
  <div class="lunboenterpage">
    <van-overlay
      :show="show"
      @click="overlay"/>
    <logicheader v-bind:title="lunbonamemsg"></logicheader>
    <!--表头按钮-->
    <div class="tableheader">
      <div class="boxw">
        <div @click="fenleibtn(1)">{{lunbonamemsg}}</div>
        <div class="boxw_middle" @click="fenleibtn(2)">排序</div>
        <div @click="fenleibtn">筛选</div>
      </div>
    </div>
    <!--分类部分-->
    <div class="fenlei" v-show="isopc1">
      <li class="lileft" v-for="(obj,index) in shoplistdata" @click="getrightdata(obj.id)" :style="{background:isbgc}">
        <div class="img">
          <img :src="getImgPath(obj.image_url)" alt="">
        </div>
        <span>{{obj.name}}</span>
        <span class="right">{{obj.count}}
 <van-icon name="arrow" class="arrow"></van-icon>
        </span>
      </li>
      <ul class="liright">
        <li v-for="k in rightdata.slice(1)">
          <span>{{k.name}}</span>
          <span class="right">{{k.count}}
        </span>
        </li>
      </ul>

    </div>
    <!--排序部分-->
    <div class="paixu" v-show="isopc2">
      <li @click="paixudata(4)">
        <i class="el-icon-sort"></i>
        <p>
          <span class="pname">智能排序</span>
        </p>
      </li>
      <li @click="paixudata(5)">
        <i class="el-icon-location-outline"></i>
        <p>
          <span class="pname">距离最近</span>
        </p>
      </li>
      <li @click="paixudata(6)">
        <van-icon name="fire-o" class="fire-o"/>
        <p>
          <span class="pname">销量最高</span>
        </p>
      </li>
      <li @click="paixudata(1)">
        <van-icon name="after-sale" class="after-sale"/>
        <p>
          <span class="pname">起送价最低</span>
        </p>
      </li>
      <li @click="paixudata(2)">
        <i class="el-icon-time"></i>
        <p>
          <span class="pname">配送速度最快</span>
        </p>
      </li>
      <li @click="paixudata(3)">
        <i class="el-icon-star-off"></i>
        <p>
          <span class="pname">评分最高</span>
        </p>
      </li>
    </div>
    <!--筛选部分-->
    <div class="shaisuan" v-show="isopc3">
      <!--  筛选下拉框 -->
      <div v-if="isopc3" class="screen">
        <div class="screen_top">
          <span class="sendfun">配送方式</span>
          <van-row>
            <van-col span="8">
              <p @click="ischecked('蜂鸟专送')" class="screen_btn">
                <van-icon name="logistics"/>
                <!--<i class="el-icon-check"></i>-->
                <span :class="{bgc:ischeck =='蜂鸟专送'}">蜂鸟专送</span>
              </p>
            </van-col>

            <van-col class="sendfun" span="24">商家属性(可以多选)</van-col>

            <van-col span="8">
              <p @click="ischecked('品牌商家')" class="screen_btn btn_6">
                <span class="pin">品</span>
                <!--<i class="el-icon-check"></i>-->
                <span :class="{bgc:ischeck =='品牌商家' }">品牌商家</span>
              </p>
            </van-col>
            <van-col span="8">
              <p type="default" class="screen_btn btn_6">
                <span class="bao">保</span>
                <!--<i class="el-icon-check"></i>-->
                <span>外卖保</span>
              </p>
            </van-col>
            <van-col span="8">
              <p type="default" class="screen_btn btn_6">
                <span class="zhun">准</span>
                <!--<i class="el-icon-check"></i>-->
                <span>准时达</span>
              </p>
            </van-col>

            <van-col span="8">
              <p type="default" class="screen_btn btn_6">
                <span class="xin">新</span>
                <!--<i class="el-icon-check"></i>-->
                <span>新店</span>
              </p>
            </van-col>
            <van-col span="8">
              <p type="default" class="screen_btn btn_6">
                <span class="fu">付</span>
                <!--<i class="el-icon-check"></i>-->
                <span>在线支付</span>
              </p>
            </van-col>
            <van-col span="8">
              <p type="default" class="screen_btn btn_6">
                <span class="piao">票</span>
                <!--<i class="el-icon-check"></i>-->
                <span>开发票</span>
              </p>
            </van-col>

          </van-row>
        </div>
        <!--  清空  确认按钮  -->
        <div class="clear_sure">
          <van-row>
            <van-col span="12">
              <p class="clear1">清空</p>
            </van-col>
            <van-col span="12">
              <p class="sure">确定</p>
            </van-col>
          </van-row>
        </div>
      </div>


    </div>
    <!--列表数据部分-->
    <div class="bott">
      <!--、商品信息-->
      <div class="center clearfix">
        <!--商品循环列表-->
        <div v-for="(item,index) in rest" class="rest-a">
          <img v-if="item.image_path" :src="'http://elm.cangdu.org/img/'+ item.image_path" class="urlimg">
          <span class="pinpai">品牌</span>
          <span class="mingzi">{{item.name}}</span>
          <span class="baopiao right">保 准 票</span>
          <!--vant 星星-->
          <div class="clearfix" style="margin-top: 0.16rem">
            <van-rate :size="2"
                      value="5"
                      color="gold"
                      void-icon="star"
                      void-color="wheat" v-model="item.rating" class="vantRa left"/>
            <span class="pingfen left">{{item.rating}}</span>
            <span class="yueshou">月售{{item.recent_order_num}}单</span>
            <span class="fengniao" v-if="item.delivery_mode">{{item.delivery_mode.text}}</span>
            <span class="zhunshida">准时达</span>
          </div>
          <div class="clearfix">
            <span class="peisong left">￥{{item.float_minimum_order_amount}}起送/配送费￥{{item.float_delivery_fee}}</span>
            <span class="gongli">{{item.distance}} /</span>
            <span class="fenzhong">{{item.order_lead_time}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>


</template>

<script>
  import Vue from "vue"
  import Logicheader from "../logicheader";


  export default {
    name: "lunboenterpage",
    components: {Logicheader},

    data() {
      return {
        rest: [],
        shopname: "",//点击位置的商家名字
        shoplatitude: "",//当前店家的纬度信息
        shoplongitude: "",//当前店家的经度信息
        geohash: "",//商店经纬度信息
        lunbonamemsg: "",//分类名称
        isopc1: false,//列表显示隐藏
        isopc2: false,//列表显示隐藏
        isopc3: false,//列表显示隐藏
        shoplistdata: "",//商铺分类列表
        show: false,//
        rightdata: "",//右列表数据
        isbgc: "",//点击时背景色
        ischeck: '',//点击时背景色
      }
    },
    created() {

      //从vuex中取数据localname
      let vuexlocalname = this.$store.state.localname;
      this.lunbonamemsg = vuexlocalname

      //从vuex中取数据geohash
      let vuexgeohash = this.$store.state.localgeohash;
      this.geohash = vuexgeohash

      //从vuex中取数据latitude
      let vuexlatitude = this.$store.state.lacalshoplatitude;
      this.shoplatitude = vuexlatitude

      //从vuex中取数据longitude
      let vuexlongitude = this.$store.state.lacalshoplongitude;
      this.shoplongitude = vuexlongitude


      Vue.axios.get(`/v2/pois/${this.geohash}`).then((res) => {
        this.shopname = res.data.name
        this.shoplatitude = res.data.latitude
        this.shoplongitude = res.data.longitude
      }).catch((error) => {
        console.log(error);
      })


    },

    mounted() {
      // 获取商品数据
      this.paixudata(4)
      // this.getgoodsdata()
      // 获取商铺分类列表
      this.getshopdata()
      //获取排序列表

    },

    methods: {
      // 分类右侧列表
      getrightdata(v) {
        // this.isbgc = "#fff"
        var res = this.shoplistdata.filter(item => {
          return item.id == v
        })
        this.rightdata = res[0].sub_categories;

      },
      //获取列表图标
      getImgPath(v) {
        //传递过来的图片地址需要处理后才能正常使用
        let suffix;
        if (!v) {
          return '//elm.cangdu.org/img/default.jpg'
        }
        if (v.indexOf('jpeg') !== -1) {
          suffix = '.jpeg'
        } else {
          suffix = '.png'
        }
        let url = '/' + v.substr(0, 1) + '/' + v.substr(1, 2) + '/' + v.substr(3) + suffix;
        return 'https://fuss10.elemecdn.com' + url
      },

      //收起分类列表
      overlay() {
        this.show = false
        this.isopc1 = false;
        this.isopc2 = false;
        this.isopc3 = false;
      },

      // 分类选择事件
      fenleibtn(v) {
        if (v == 1) {
          this.isopc1 = !this.isopc1;
          this.show = false;
          this.isopc2 = false;
          this.isopc3 = false;
          if (this.isopc1) {
            this.show = true;
          }
        } else if (v == 2) {
          this.isopc1 = false;
          this.isopc2 = !this.isopc2;
          this.show = false;
          this.isopc3 = false;
          if (this.isopc2) {
            this.show = true;
          }
        } else {
          this.isopc1 = false;
          this.isopc2 = false;
          this.isopc3 = !this.isopc3;
          this.show = false;
          if (this.isopc3) {
            this.show = true;
          }
        }
      },

      getshopdata() {
        Vue.axios.get("/shopping/v2/restaurant/category", {}).then((result) => {
          this.shoplistdata = result.data
        }).catch((error) => {
          console.log(error);
        })
      },
      paixudata(v) {
        this.show = false
        this.isopc2 = false;
        Vue.axios.get(`shopping/restaurants?latitude=${this.shoplatitude}&longitude=${this.shoplongitude}`, {
          params: {
            order_by: v
          }
        }).then((res) => {
          this.rest = res.data
          console.log(res);
        }).catch((error) => {
          console.log(error);
        })
      },
      ischecked(v) {
        this.ischeck = v
        console.log(0);
      },
    }


  }
</script>

