  .lunboenterpage {
    margin-top: 1rem;
    /*表头样式*/
    .tableheader {
      border-bottom: .002rem solid #e4e4e4;

      .boxw {
        width: 100%;
        text-align: center;
        font-size: 0;
        position: fixed;
        background: white;
        top: 1rem;
        z-index: 2;
        div {
          display: inline-block;
          font-size: 0.26rem;
          margin: 0.2rem 0;
          line-height: .4rem;
          width: 33.3%;
        }
        .boxw_middle {
          border-right: .025rem solid #e4e4e4;
          border-left: .025rem solid #e4e4e4;
        }
      }
    }
    /*分类菜单样式*/
    .fenlei {
      animation: fadeInDown 0.25s linear forwards;
      width: 100%;
