          border-bottom: .025rem solid #e4e4e4;
        }
      }
    }
    /*排序菜单样式*/
    .paixu {
      animation: fadeInDown 0.25s linear forwards;
      background: white;
      top: 1.8rem;
      z-index: 1;
      padding-left: 0.5rem;
      position: fixed;
