          }
          /*保准票*/
          .baopiao {
            margin-top: 0;
          }
          div {
            /*星星*/
            .vantRa {
              margin-top: -1rem;
              margin-left: 1.85rem;
            }
            /*评分*/
            .pingfen {
              color: red;
