</script>

<style scoped lang="less">
  .is-active {
    border-bottom: .05rem solid #3190e8
  }
.mine{
  background-color: #f6f6f6;
  .top{
    width: 100%;
    background-color: white;
    display: inline-block;
    margin-top: 1rem;
    a{
      display: inline-block;
      font-size: .3rem;
      margin: .3rem .2rem .2rem 1.7rem;
      padding-bottom: .1rem;
    }
  }

}
</style>



