<template>
  <div class="citypage">
    <!--引入的头组件-->
    <cityheader v-bind:citypage="city"></cityheader>
    <!--地址输入框-->
    <div class="locatinput">
      <input v-model="inputval" type="text" placeholder="输入学校、商务楼、地址">
      <!--输入框内的删除按钮-->
      <i class="iconfont deletebtn" @click="deketeinputval" :style="{opacity:isdeleBtnOpc}">&#xe604;</i>
      <!--提交数据搜索按钮-->
      <button @click="submitmsg(inputval)" class="loginbtn">提交</button>
    </div>
    <!--历史记录列表-->
    <div class="houstory" :style="{opacity:isHoustoryOpc}">搜索历史</div>
    <!--展示历史记录列表-->
    <div class="houstoryshow">
      <li v-for="c in searchcityres" @click="towaimaipage(c)">
        <h4>{{c.name}}</h4>
        <p>{{c.address}}</p>
      </li>
    </div>


  </div>
</template>

<script>

  import Cityheader from "./cityheader";
  import Vue from "vue"

  export default {
    name: "citypage",
    components: {Cityheader},
    data() {
      return {
        inputval: "",//地址输入框内容
        isdeleBtnOpc: 0,//地址栏删除按钮显示隐藏
        isHoustoryOpc: 1,//历史记录表头显示隐藏
        city: "",//城市名字
        cityid: "",//城市id
        searchcityres: "",//按输入框内容搜索结果
        geohash: {},//经纬度信息
        shopinfor: {},//点击的位置信息`
        latitude:"",//纬度信息
        longitude:"",//经度信息
      }
    },
    methods: {
      // 点击提交输入的内容
      submitmsg(inputval) {
        // 点击隐藏历史记录表头
        this.isHoustoryOpc = 0;
        // 判断输入框的值
        if (inputval.length !== 0) {
          // 传值发起get请求(传值)
          Vue.axios.get("/v1/pois", {
            params: {
              keyword: this.inputval,
              city_id: this.cityid,
            }
          }).then((result) => {
            this.searchcityres = result.data

          }).catch((error) => {
            console.log(error);
          })
        }
      },
      // 点击具体位置进入商品页面
      towaimaipage(v) {
        this.$router.push({path:"/waimaipage"})

        // 将geohash储存在vuex中
        this.$store.commit('savelocalgeohash',v.geohash)

        // 将latitude储存在vuex中
        this.$store.commit('savelacalshoplatitude',v.latitude)

        // 将longitude储存在vuex中
        this.$store.commit('savelacalshoplongitude',v.longitude)




      },
      // 按钮删除事件
      deketeinputval() {
        this.inputval = ""
        this.isdeleBtnOpc = 0
      }
    },
    // 监听输入框事件
    watch: {
      inputval(inputval) {
        if (inputval.length === 0) {
          this.isdeleBtnOpc = 0
        } else {
          this.isdeleBtnOpc = 1;
        }
      }
    },
    // 钩子函数
    created() {
      // 接收index传过来的值
      this.city = this.$route.query.city;
      this.cityid = this.$route.query.cityid;
    },
  }
</script>

<style scoped lang="less">
  /*地址输入框*/
  .locatinput {
    display: inline-block;
    position: relative;
    width: 100%;
    margin-top: 1rem;
    padding: 0.2rem;
    background: white;
    input {
      width: 100%;
      height: 0.6rem;
      border: 1px solid #e4e4e4;
      padding: 0 .1rem;
      font-size: .24rem;
      margin-bottom: 0.2rem;
      color: #333
    }
    .deletebtn {
      font-size: 0.35rem;
      font-weight: bold;
      color: #4a6fff;
      position: absolute;
      top: 0.38rem;
      right: 0.4rem;
      background: transparent;
      opacity: 0;

    }
    .loginbtn {
      width: 100%;
      height: 0.6rem;
      background-color: #3190e8;
      font-size: .3rem;
      border-radius: 0.04rem;
      color: #fff;
    }
  }

  /*历史记录列表*/
  .houstory {
    font-size: .21rem;
    padding: 0.08rem 0.2rem 0.08rem 0.2rem;
    border-top: 1px solid #e4e4e4;
    border-bottom: 1px solid #e4e4e4;
  }

  /*展示历史记录列表*/
  .houstoryshow {
    li {
      margin: 0 auto;
      padding-top: .05rem;
      border-bottom: 1px solid #e4e4e4;
      h4 {
        margin: 0.2rem 0.35rem;
        width: 90%;
        font-size: .3rem;
        color: #333;
      }
      p {
        width: 90%;
        margin: 0 auto .25rem;
        font-size: .2rem;
        color: #999;
      }
    }
  }

</style>



