<template>
  <div class="wind">
    <!--头部-->
    <div class="header_top">
      <van-icon name="arrow-left" class="van2" @click="back"/>
      <span>兑换红包</span>
    </div>
    <!--中间输入框验证码-->
    <div class="center_middle">
      <p class="p1">
        <input type="number" placeholder="请输入兑换码" v-model="input1">
      </p>
      <section class="p2">
        <input type="text" placeholder="验证码" v-model="input2" oninput="if(value.length>4)value=value.slice(0,4)">
        <div class="p2_right">
          <img :src="array">
          <p class="invisibility" @click="getclick">看不清</p>
          <p class="cut" @click="getclick">换一张</p>
        </div>
      </section>
    </div>
    <!--底部兑换按钮-->
    <button class="conversion" @click="exchange" :disabled="disabled" :style="{'background':bgc}">兑换</button>
    <!--弹出的框动画-->
    <transition enter-active-class="animated bounceIn" leave-active-class="opacity 0">
      <div class="kuang" v-if="show">
        <van-icon name="warning-o"  class="van1"/>
        <p>无效的兑换码</p>
        <button @click="confirm">确认</button>
      </div>
    </transition>

  </div>
</template>

<script>
  import Vue from "vue"
  export default {
    name: "redpacket",
    data() {
      return {
        array:'',
        username:"",
        input1:"",
        input2:"",
        show:false,
        disabled:true,
        isinput1:"",
        isinput2:"",
        bgc:"#ccc",
      }
    },
    methods:{
      getclick(){
        Vue.axios.post("https://elm.cangdu.org/v1/captchas").then((result)=>{
          this.array=result.data.code;
        }).catch((error)=>{
          console.log(error);
        })
      },
      back(){
        this.$router.go(-1);
      },
      confirm(){
        this.show=false;
      },
      //点击兑换红包
      exchange(){
        Vue.axios.post(`v1/users/${this.usrename}/hongbao/exchange`,{
          exchange_code:this.input1,
          captcha_code:this.input2,
        }).then((result)=>{
          if(result.data.message=="无效的兑换码"){
            this.show=true;
          }
        }).catch((error)=>{
          console.log(error);
        })
      }
    },
    created(){
      this.getclick();
      //把localStorage中的用户名信息取出来
      let localData = JSON.parse(localStorage.getItem('localdata'));
      this.username=localData.username;
    },
    watch:{
      input1(){
        if(this.input1.length!=0){
          this.isinput1=true;
        }else{
          this.isinput1=false;
        }
        if(this.isinput1==true&&this.isinput2==true){
          this.bgc = "#4cd964";
          this.disabled=false;
        }
      },
      input2(){
        if(this.input2.length==4){
          this.isinput2=true;
        }else{
          this.isinput2=false;
        }
        if(this.isinput1==true&&this.isinput2==true){
          this.bgc = "#4cd964";
          this.disabled=false;
        }
      }
    }
  }
</script>

<style scoped lang="less">

  .wind {
    width: 100%;
    height: 100%;
  }

  .header_top {
    width: 100%;
    height: 0.9rem;
    background-color: rgb(49, 144, 232);
  }

  .header_top span {
    color: white;
    font-size: 0.4rem;
    font-weight: bold;
    margin-left: 2.1rem;
    vertical-align: -0.35rem;
  }

  .center_middle {
    width: 100%;
    height: 2.4rem;
    background-color: rgb(241,241,241);
  }

  .p1 {
    width: 6.8rem;
    height: 0.9rem;
    margin-top: 0.3rem;
    margin-left: 0.4rem;
    font-size: 0.35rem;
    border-radius: 0.1rem;
    background-color: white;
  }

  .p1 input {
    margin-left: 0.1rem;
    margin-top: 0.2rem;
    font-size:.32rem;
  }
  .p2{
    width: 4rem;
    height: 0.9rem;
    margin-top: 0.3rem;
    margin-left: 0.4rem;
    font-size: 0.35rem;
    border-radius: 0.1rem;
    background-color: white;
    float: left;
  }
  .p2 input{
    margin-left: 0.1rem;
    margin-top: 0.2rem;
    font-size:.32rem;
  }
  .p2_right{
    width: 2.5rem;
    height: 0.9rem;
    background-color: white;
    margin-left: 4.3rem;
    margin-top: -0.63rem;
    border-radius: 0.2rem;
  }
  .p2_right img{
    width: 1.3rem;
    height: 0.8rem;
    margin-left: 0.1rem;
  }
  .p2_right p{
    font-size: 0.2rem;
  }
  .invisibility{
    font-size: 0.2rem;
    float: right;
    margin-right: 0.15rem;
    margin-top: 0.15rem;
  }
  .cut{
    float: right;
    margin-top: -0.35rem;
    margin-right: 0.15rem;
    color: #3b95e9;
  }
  .conversion{
    width: 6.8rem;
    height: 0.9rem;
    background-color: #ccc;
    font-size: 0.4rem;
    color: #fff;
    text-align: center;
    line-height: 0.8rem;
    border-radius: 0.1rem;
    margin-left: 0.4rem;
  }
  .van2{
    padding: .2rem;
    color: white;
    font-size: .4rem;
    vertical-align: -.3rem;
  }
  /*弹出的框动画*/
  .kuang {
    width: 74%;
    background-color: #fff;
    text-align: center;
    position: absolute;
    left: 13%;
    top: 3.6rem;
    border-radius: .25rem .25rem;
  .van1 {
    padding-top: .2rem;
    font-size: 1.5rem;
    color: #f8cb86;
  }
  p {
    font-size: .3rem;
    color: #333;
    line-height: .4rem;
    margin-top: .3rem;
    padding-bottom: .3rem;
  }
  button {
    font-size: .4rem;
    color: #fff;
    font-weight: 700;
    margin-top: .4rem;
    background-color: #4cd964;
    width: 100%;
    line-height: .9rem;
    border-radius: 0 0 .25rem .25rem;
  }
  }
</style>



