<template>
  <transition enter-active-class="animated shake" leave-active-class="opacity 0">
    <div class="mine">
      <my-header></my-header>
      <div class="first">
        <ul>
          <li v-for="(once,k) in dataArr" :key="k">
            <h3>{{once[0][0]+once[0][1]}}</h3>
            <p v-for="(one,k1) in once[1]" :key="k1">
              {{one}}
            </p>
          </li>
        </ul>
      </div>
    </div>
  </transition>


</template>

<script>
  import Vue from "vue"
  import MyHeader from "./myHeader";
    export default {
      name: "pointshuoming",
      data(){
        return {
          dataArr:[],
        }
      },
      components: {MyHeader},
      mounted(){
          Vue.axios.get("/v3/profile/explain").then((result)=>{
            let array1=result.data.pointtextContent.split("###");
            // console.log(array1)
            array1.splice(0,1);
            let a={};
            array1.forEach((v,k)=>{
              let b=v.split(" ");
              // console.log(b)
              for (let i=0; i < b.length; i++) {
                if (b[i] === "*" || (b[i] === '')) {
                  b.splice(i,1);
                  // console.log(b)
                  i--;
                }
              }
              let c=b.splice(0,2);
              a[k]=[c,b];
            });
            this.dataArr=a;
          });

      }
    }
</script>

<style scoped lang="less">
  .mine{
    animation: fadeInRight 0.35s linear forwards;
    height: 100%;
    background-color: white;
  .first{
    width: 100%;
    display: inline-block;
    margin-top: 1.3rem;
    padding-left: .3rem;
  h3{
    color: #333;
    font-weight: 400;
    font-size: .33rem;
    box-sizing: border-box;
    margin-bottom: .2rem;
    line-height: .7rem;
  }
  p,li{
    font-size: .28rem;
    color: #666;
    line-height: .4rem;
    padding: 0 .1rem;
  }
  }
  }
</style>



