<template>
  <div class="orderpage">
    <!--头部-->
    <logicheader v-bind:title="msgtitle"></logicheader>
    <!--订单列表-->
    <div class="box">
      <div>
        <img src="//elm.cangdu.org/img/164ad0b6a3917599.jpg" alt="">
        <div class="font">
          <span class="gooodsname">{{this.orderlistinfor.restaurant_name}}></span>
          <span class="ordertime">2019年8月13日18:45:37</span>
          <div class="orderzhuangtai">等待支付</div>
        </div>
        <p class="goodsdesc">
          <span class="descname">{{goodsname}}</span>
          <span class="allnum">等{{goodsnum}}件商品</span>
          <span class="allprice right">¥{{goodsprice}}</span>
        </p>
        <p class="ordertimea">
          <span class="lasttime clearfix">去支付还剩 <van-count-down
            millisecond
            :time="time"
            format="mm 分 ss 秒"
            class="van right" :auto-start="true" @finish="finished"/></span>
        </p>
      </div>
      <!--弹出的框动画-->
      <transition enter-active-class="animated bounceIn" leave-active-class="opacity 0">
        <div class="kuang" v-if="show">
          <van-icon name="warning-o" class="van1"/>
          <p>订单已失效,请重新下单!</p>
          <button @click="confirm">确认</button>
        </div>
      </transition>

    </div>
    <!--底部-->
    <childfooter v-bind:tomsg="msg"></childfooter>
  </div>
</template>

<script>
  import Vue from "vue"
  import Childfooter from "./goodspage/common/childfooter";
  import Logicheader from "./logicheader";

  export default {
    name: "orderpage",
    components: {Logicheader, Childfooter},
    data() {
      return {
        msgtitle: "订单列表",
        msg: "订单",//底部按钮颜色
        userid: "",//登录用户id
        orderlistinfor: "",//订单列表信息
        time: 15 * 60 * 1000,//
        goodsnum: "",//商品数量
        goodsprice: "",//商品总价
        goodsname: "",//商品名字
        show: false,//弹框显示隐藏
        newtime:"",//下单时间
      }
    },
    methods: {
      finished() {
        this.show = true;
      },
      confirm() {
        this.show = false;
      },
    },
    created() {
      localStorage.tomsg = '订单'
      this.userid = this.$store.state.userStatus.user_id
    },
    mounted() {
      Vue.axios.get(`/bos/v2/users/${this.userid}/orders?limit=10&offset=0`, {}).then((res) => {
        this.orderlistinfor = res.data[0]
        this.goodsnum = this.orderlistinfor.basket.packing_fee.category_id
        this.goodsprice = this.orderlistinfor.basket.packing_fee.price;
        this.goodsname = this.orderlistinfor.basket.group[0][0].name;
        this.$store.commit('saveorderlistinfor', this.orderlistinfor)
      }).catch((error) => {
        console.log(error);
      })
    }
  }
</script>

<style scoped lang="less">

  .box {
    width: 100%;
    height: 3.0rem;
    margin-top: 1rem;
    padding: 0.1rem 0.2rem 0.2rem 0.2rem;
    background: white;
    div {
      img {
        width: 1rem;
      }
      .font {
        display: inline-block;
        width: 80%;
        position: relative;
        padding-bottom: 0.1rem;
        .gooodsname {
          font-size: 0.4rem;
          line-height: 0.9rem;
        }
        .ordertime {
          display: block;
        }
        .orderzhuangtai {
          display: inline-block;
          font-size: 0.27rem;
          position: absolute;
          right: 0;
          top: 0.28rem;
        }
      }
      .goodsdesc {
        margin-left: 1.1rem;
        font-size: 0.2rem;
        line-height: 0.8rem;
        border-bottom: 0.5px solid #c5c5c5;
        border-top: 0.5px solid #c5c5c5;
        padding-right: 0.2rem;
        .allprice {
          color: #f60;
        }
      }
      .ordertimea {
        font-size: 0.2rem;
        margin: 0.2rem;
        line-height: 0.4rem;
        position: absolute;
        border: 0.8px solid #f60;
        border-radius: 4px;
        top: 3.2rem;
        right: 0;
        .lasttime {
          padding: 0 0.1rem;
          display: inline-block;
          width: 2.3rem;
          color: #f60;
          .van {
            font-size: .02rem;
            color: #f60;
          }
        }
      }
    }
    .kuang {
      width: 74%;
      background-color: #fff;
      text-align: center;
      position: absolute;
      left: 13%;
      top: 4rem;
      border-radius: .25rem .25rem 0 0;
      .van1 {
        padding-top: .2rem;
        font-size: 1.5rem;
        color: #f8cb86;
      }
      p {
        font-size: .3rem;
        color: #333;
        line-height: .4rem;
        margin-top: .3rem;
        padding-bottom: .3rem;
      }
      button {
        font-size: .4rem;
        color: #fff;
        font-weight: 700;
        margin-top: .4rem;
        background-color: #4cd964;
        width: 100%;
        line-height: .9rem;
        border-radius: 0 0 .25rem .25rem;
      }
    }
  }
</style>



