<template>
  <div class="mine">
    <my-header></my-header>
    <!--输入框部分-->
    <div class="top">
      <input type="text" placeholder="请填写你的姓名" v-model="input1" :style="{'border':isTrue}">
      <p v-if="show">请填写您的姓名</p>
      <input type="text" placeholder="校区/写字楼/学校等" @click="searchaddress" v-model="input2">
      <input type="text" placeholder="请填详细送餐地址" v-model="input3" :style="{'border':isTrue1}">
      <p v-if="show1">请详细填写送餐地址</p>
      <p v-if="show2">送餐地址太短了，不能辨识</p>
      <input type="text" placeholder="请填能够联系到您的手机号" v-model="input4" :style="{'border':isTrue2}"
             oninput="if(value.length>11)value=value.slice(0,11)">
      <P v-if="show3">手机号不能为空</P>
      <p v-if="show4">请输入正确的手机号</p>
      <input type="text" placeholder="备用联系电话（选填）" v-model="input5" :style="{'border':isTrue3}"
             oninput="if(value.length>11)value=value.slice(0,11)">
      <p v-if="show6">请输入正确的手机号</p>
    </div>
    <button class="btn1" @click="add">新增地址</button>
    <!--弹出的框动画-->
    <transition enter-active-class="animated bounceIn" leave-active-class="opacity 0">
      <div class="kuang" v-if="show5">
        <van-icon name="warning-o" class="van1"/>
        <p>{{words}}</p>
        <button @click="confirm">确认</button>
      </div>
    </transition>
  </div>
</template>

<script>
  import Vue from "vue"
  import MyHeader from "./myHeader";

  export default {
    name: "addAddress",
    components: {MyHeader},
    data() {
      return {
        input1: "",
        input2: "",
        input3: "",
        input4: "",
        input5: "",
        oldname: {},
        oldaddress: {},
        oldgeohash: {},
        show: false,
        words: "",
        isTrue: "1px solid #ddd",
        isTrue1: "1px solid #ddd",
        isTrue2: "1px solid #ddd",
        isTrue3: "1px solid #ddd",
        show1: false,
        show2: false,
        show3: false,
        show4: false,
        show5: false,
        show6: false,
        localData: "",
        tag: "标签",
        tag_type: 1,
      }
    },
    methods: {
      searchaddress() {
        this.$store.commit("saveAddressname", this.input1)
        this.$router.push({path: '/searchaddress', query: {name: '搜索地址'}})
      },
      //点击新增地址方法
      add() {
        Vue.axios.post(`v1/users/${this.localData.user_id}/addresses`, {
          address: this.oldaddress,
          address_detail: this.input3,
          geohash: this.oldgeohash,
          name: this.input1,
          phone: this.input4,
          tag: this.tag,
          sex: 1,
          phone_bk: this.input5,
          tag_type: this.tag_type,
        }).then((result) => {
          // console.log(result.data);
          if (result.data.message == "详细地址信息错误") {
            this.show5 = true;
            this.words = "详细地址信息错误";
          } else if (result.data.message == "地址信息错误") {
            this.show5 = true;
            this.words = "地址信息错误";
          } else if (result.data.message == "收货人姓名错误") {
            this.show5 = true;
            this.words = "收货人姓名错误";
          } else if (result.data.message == "收货人手机号错误") {
            this.show5 = true;
            this.words = "收货人手机号错误";
          } else {
            console.log(result);
            this.$router.push({path: "/address"})
          }
        }).catch((error) => {
          // console.log(error);
        })
      },
      confirm() {
        this.show5 = false;
      }
    },
    created() {
      this.input1 = this.$store.state.addressname
      this.oldname = this.$route.query.oldname;
      this.input2 = this.oldname;
      this.oldaddress = this.$route.query.oldaddress;
      this.oldgeohash = this.$route.query.oldgeohash;
      this.localData = this.$store.state.userStatus;
    },
    mounted(){
    },
    //判断输入框的样式
    watch: {
      input1() {
        if (this.input1.length == "") {
          this.isTrue = "1px solid red";
          this.show = true;
        } else {
          this.isTrue = "1px solid #ddd";
          this.show = false;
        }
      },
      input3() {
        if (this.input3.length == "") {
          this.isTrue1 = "1px solid red";
          this.show1 = true;
          this.show2 = false;
        } else if (this.input3.length > 0 && this.input3.length < 3) {
          this.show1 = false;
          this.show2 = true;
        } else {
          this.isTrue1 = "1px solid #ddd";
          this.show1 = false;
          this.show2 = false;
        }
      },
      input4() {
        if (this.input4.length == "") {
          this.isTrue2 = "1px solid red";
          this.show3 = true;
          this.show4 = false;
        } else if (!(/^1[34578]\d{9}$/.test(this.input4))) {
          this.show3 = false;
          this.show4 = true;
        } else {
          console.log(1)
          this.isTrue2 = "1px solid #ddd";
          this.show3 = false;
          this.show4 = false;
        }
      },
      input5() {
        if (this.input5.length == "") {
          this.isTrue3 = "1px solid red";
          this.show6 = true;
        } else if (!(/^1[34578]\d{9}$/.test(this.input5))) {
          this.show6 = true;
        } else {
          this.isTrue3 = "1px solid #ddd";
          this.show6 = false;
        }
      },
    },

  }
</script>

<style scoped lang="less">
  .mine {
    .top {
      display: inline-block;
      margin-top: 1.2rem;
      padding-top: .01rem;
      width: 100%;
      background-color: white;
      input {
        width: 94%;
        margin: .2rem 3%;
        padding: .13rem;
        display: block;
        background-color: #f2f2f2;
        border: .01rem solid #ddd;
        border-radius: .1rem;
        font-size: .28rem;
      }
      p {
        font-size: .2rem;
        color: #ea3106;
        padding-left: .2rem;
        margin-top: .1rem;
      }
    }
    .btn1 {
      width: 94%;
      font-size: .3rem;
      color: #fff;
      margin: .2rem 3%;
      line-height: .8rem;
      background: #00dc6e;
      font-weight: 700;
      opacity: .6;
      border-radius: .1rem;
    }
    .kuang {
      width: 74%;
      background-color: #fff;
      text-align: center;
      position: absolute;
      left: 13%;
      top: 4rem;
      border-radius: .25rem;
      .van1 {
        padding-top: .2rem;
        font-size: 1.5rem;
        color: #f8cb86;
      }
      p {
        font-size: .3rem;
        color: #333;
        line-height: .4rem;
        margin-top: .3rem;
        padding-bottom: .3rem;
      }
      button {
        font-size: .4rem;
        color: #fff;
        font-weight: 700;
        margin-top: .4rem;
        background-color: #4cd964;
        width: 100%;
        line-height: .9rem;
        border-radius: 0 0 .25rem .25rem;
      }
    }
  }

</style>



