<template>
  <div class="mine">
    <my-header></my-header>
    <div class="top">
      <input type="file">
      <h2>头像</h2>
      <img src="//elm.cangdu.org/img/default.jpg" alt="" class="img1">
      <van-icon name="arrow" class="van1"/>
      <!--用户名-->
      <router-link :to="{path:'/changeusername',query:{name:'修改用户名'}}">
        <span class="span1">用户名</span>
        <span class="span2 ">{{allInfro.username}}</span>
        <van-icon name="arrow" class="van2 right"/>
      </router-link>
      <!--收货地址-->
      <router-link :to="{path:'/address'}" class="a2">
        <span class="span1">收货地址</span>
        <van-icon name="arrow" class="van2 right"/>
      </router-link>
      <!--账号绑定-->
      <p>账号绑定</p>
      <button @click="phone">
        <img class="img2"
             src="data:image/png;base64,"
             alt="">
        <span>手机</span>
        <van-icon name="arrow" class="van right"/>
      </button>
      <!--点击手机弹框-->
      <transition enter-active-class="animated bounceIn" leave-active-class="opacity 0">
        <div class="bottom" v-if="show">
          <van-icon name="warning-o" class="van4"/>
          <h4>请在手机APP中设置</h4>
          <button class="btn3" @click="confirm">确认</button>
        </div>
      </transition>
      <!--安全设置-->
      <p @click="abc">安全设置</p>
      <router-link :to="{path:'/changepassword',query:{name:'重置密码'}}" class="a2">
        <span class="span1">登录密码</span>
        <span class="span2 span3">修改</span>
        <van-icon name="arrow" class="van2 right"/>
      </router-link>
      <!--退出登录按钮-->
      <div class="btn">
        <button class="btn1" @click="backdl">退出登录</button>
      </div>

    </div>
    <div class="bgc" v-if="show1"></div>
    <!--点击退出登录弹框-->
    <transition enter-active-class="animated bounceIn" leave-active-class="opacity 0">
      <div class=" bto" v-if="show1">
        <van-icon name="warning-o" class="van4"/>
        <h1>是否退出登录</h1>
        <button class="btn4" @click="wait">再等等</button>
        <button class="btn5" @click="back">退出登录</button>
      </div>
    </transition>

  </div>
</template>

<script>
  import MyHeader from "./myHeader";

  export default {
    name: "account",
    components: {MyHeader},
    data() {
      return {
        show: false,
        show1: false,
        allInfro: "",
      }
    },
    mounted() {

      this.allInfro = this.$store.state.userStatus

    },
    methods: {
      abc() {
        this.$router.push({path: '/index'})
      },
      phone() {
        this.show = true;
      },
      confirm() {
        this.show = false;
      },
      backdl() {
        this.show1 = true;
      },
      wait() {
        this.show1 = false;
      },
      //退出登录
      loginOut() {
        this.$store.commit('saveUserStatus', null); // 清除Vuex
        this.$store.commit('saveisrightopc',null);
        localStorage.clear()
        // localStorage.removeItem('localdata');  // 清除localStorage
        // localStorage.removeItem('localisrightopc');
        console.log('退出成功');
        // localStorage.setItem('localisrightopc', JSON.stringify(this.isrightopc))
      },

      back() {
        // Vue.axios.get("/v2/signout").then((res)=>{
        //   console.log(res);
        // }).catch((error)=>{
        //   console.log(error);
        // })
        this.loginOut()


        // this.$store.state.userStatus = ""
        // this.$store.state.isrightopc = ""
        // this.$store.state.localgeohash = ""
        // this.$store.state.localname = ""
        // this.$store.state.lacalshoplatitude = ""
        // this.$store.state.lacalshoplongitude = ""
        // this.$store.state.address = ""
        // this.$store.state.addressname = ""


        this.$router.push({path: "/index"});
      }
    }
  }
</script>


<style scoped lang="less">
  .mine {
    width: 100%;
    background-color: #f2f2f2;
    position: relative;
    .bgc {
      background-color: #000;
      position: absolute;
      top: 0;
      left: 0;
      bottom: -2.8rem;
      right: 0;
      z-index: 2;
      opacity: .2;
    }
    .bto {
      width: 96%;
      height: 60%;
      background-color: white;
      position: absolute;
      left: 2%;
      right: 2%;
      top: 27%;
      z-index: 10;
      text-align: center;
      border-radius: .1rem;
      .van4 {
        padding-top: .2rem;
        font-size: 2rem;
        color: #f8cb86;
        margin-top: .5rem;
      }
      h1 {
        margin-top: .4rem;
      }
      .btn4 {
        background: #c1c1c1;
        margin-right: .3rem;
        border-radius: .1rem;
        font-size: .3rem;
        color: #fff;
        width: 2rem;
        line-height: .75rem;
        letter-spacing: .05rem;
        margin-top: 1.3rem;
      }
      .btn5 {
        background: #dd6b55;
        border-radius: .1rem;
        font-size: .3rem;
        color: #fff;
        width: 2rem;
        line-height: .75rem;
        letter-spacing: .05rem;
      }
    }
    .top {
      display: inline-block;
      width: 100%;
      margin-top: 1.2rem;
      position: relative;
      background-color: white;
      border-top: .25px solid #ddd;
      input {
        width: 100%;
        height: 1.4rem;
        opacity: 0;
        display: block;
      }
      h2 {
        font-size: .29rem;
        color: #333;
        font-weight: 500;
        position: absolute;
        top: .5rem;
        left: .3rem;
      }
      .img1 {
        width: .9rem;
        height: .9rem;
        border-radius: 50%;
        position: absolute;
        top: .3rem;
        right: .6rem;
      }
      .van1 {
        position: absolute;
        top: .6rem;
        right: .2rem;
        color: #e4e4e4;
        font-size: .4rem;
      }
      a {
        width: 100%;
        display: inline-block;
        border-top: .2px solid #ddd;
        padding: .25rem 0 .1rem .25rem;
        .span1 {
          display: inline-block;
          font-size: .3rem;
        }
        .span2 {
          display: inline-block;
          width: 5.4rem;
          margin-left: .2rem;
          text-align: right;
          color: #999;
        }
        .van2 {
          font-size: .4rem;
          color: #e4e4e4;
          padding-right: .2rem;
          vertical-align: .2rem;
        }
      }
      .a2 {
        border-bottom: .2px solid #ddd;
      }
      p {
        background-color: #f2f2f2;
        padding: .3rem;
        font-size: .23rem;
        color: #666;
      }
      button {
        width: 100%;
        background-color: white;
        text-align: left;
        border-top: .25px solid #ddd;
        border-bottom: .25px solid #ddd;
      }
      .img2 {
        width: .43rem;
        height: .43rem;
        padding: .3rem .1rem .3rem .3rem;
      }
      span {
        vertical-align: .35rem;
      }
      .van {
        font-size: .4rem;
        color: #e4e4e4;
        padding-right: .22rem;
        padding-top: .3rem;
      }
      .span3 {
        position: absolute;
        right: .6rem;
      }
      .btn {
        width: 100%;
        background: #f2f2f2;
      }
      .btn1 {
        z-index: 5;
        width: 96%;
        margin: .7rem 2%;
        line-height: .7rem;
        border-radius: .13rem;
        text-align: center;
        background: #d8584a;
        font-size: .25rem;
        color: #fff;
      }
      .bottom {
        width: 74%;
        background-color: #fff;
        text-align: center;
        position: absolute;
        left: 13%;
        top: 2.5rem;
        border-radius: .2rem;
        .van4 {
          padding-top: .2rem;
          font-size: 1.5rem;
          color: #f8cb86;
        }
        h4 {
          font-size: .3rem;
          color: #333;
          line-height: .4rem;
          margin-top: .3rem;
          padding-bottom: .3rem;
        }
        .btn3 {
          text-align: center;
          font-size: .4rem;
          color: #fff;
          font-weight: 700;
          margin-top: .4rem;
          background-color: #4cd964;
          width: 100%;
          line-height: .9rem;
          border-radius: 0 0 .2rem .2rem;
        }
      }
    }

  }

</style>



