<template>
  <div class="apex">
    <!--头部-->
    <div class="head_top">
      <van-icon name="arrow-left" class="van2" @click="back"/>
      <span>推荐有奖</span>
    </div>
    <!--中间图片-->
    <div class="center_img">
      <img src="../img/activity.png">
    </div>
    <!--邀请-->
    <section class="invite">
      <!--微信邀请-->
      <div class="left">
        <img @click="getclick"
             src="data:image/png;base64,">
        <p>邀请微信好友</p>
      </div>
      <!--QQ邀请-->
      <div class="center">
        <img @click="getclick"
             src="data:image/png;base64,">
        <p>邀请QQ好友</p>
      </div>
      <!--面对面邀请-->
      <div class="right">
        <img @click="getclick"
             src="data:image/png;base64,">
        <p>面对面邀请</p>
      </div>
    </section>
    <!--底部-->
    <section class="detail">
      <div class="detail_left">
        <p>累计收益</p>
        <p><span>0</span>元</p>
      </div>
      <div class="detail_right">
        <p>成功邀请</p>
        <p><span>0</span>人</p>
      </div>
    </section>
    <p class="earnings">-收益明细-</p>
    <div class="earnings_bottom">
      <img
        src="data:image/png;base64,"
        alt="">
      <p>还不赶紧去邀请好友</p>
    </div>
    <transition enter-active-class="animated bounceIn " leave-active-class="opacity 0">
      <div class="kuang" v-if="show">
        <van-icon name="warning-o" class="van1"/>
        <p>请在饿了么APP中打开</p>
        <button @click="confirm">确认</button>
      </div>
    </transition>

  </div>
</template>

<script>
  export default {
    name: "prize",
    data() {
      return {
        show: false,
      }
    },
    methods: {
      getclick() {
        this.show=true;
      },
      confirm(){
        this.show=false;
      },
      back(){
        this.$router.go(-1);
      }
    }
  }
</script>

<style scoped lang="less">
  .apex {
    width: 100%;
    height: 100%;
  }

  .head_top {
    width: 100%;
    height: 0.9rem;
    background-color: rgb(49, 144, 232);
  }

  .head_top span {
    color: white;
    font-size: 0.4rem;
    font-weight: bold;
    margin-left: 2.1rem;
    vertical-align: -0.35rem;
  }

  .center_img {
    width: 100%;
    height: 4.8rem;
  }

  .center_img img {
    width: 100%;
    height: 4.7rem;
  }

  .invite {
    width: 100%;
    height: 2.5rem;
    margin-top: -0.29rem;
    background-color: white;
  }

  .left {
    width: 2rem;
    height: 1.6rem;
    margin-top: 0.4rem;
    margin-left: 0.2rem;
    text-align: center;
  }

  .left img {
    width: 1.2rem;
    height: 1.2rem;
  }

  .left p {
    font-size: 0.2rem;
    line-height: 0.5rem;
  }

  .center {
    width: 2rem;
    height: 1.6rem;
    margin-top: 0.4rem;
    margin-left: 2.7rem;
    text-align: center;
    padding-top: 0.4rem;
  }

  .center img {
    width: 1.2rem;
    height: 1.2rem;
  }

  .center p {
    font-size: 0.2rem;
    line-height: 0.5rem;
  }

  .right {
    width: 2rem;
    height: 1.6rem;
    text-align: center;
    float: right;
    margin-top: -1.2rem;
    margin-right: 0.21rem;
  }

  .right img {
    width: 1.2rem;
    height: 1.2rem;
  }

  .right p {
    font-size: 0.2rem;
    line-height: 0.5rem;
  }

  .detail {
    width: 100%;
    height: 0.7rem;
    margin-top: 0.4rem;
  }

  .detail_left {
    width: 50%;
    height: 0.7rem;
    border-right: 1px solid rgb(225, 225, 225);
    text-align: center;
  }

  .detail_left p {
    font-size: 0.1rem;
    color: #666;
  }

  .detail_left p:nth-child(2) {
    line-height: 0.5rem;
  }

  .detail_left p span {
    font-size: 0.4rem;
    color: #ff5633;
    font-weight: bold;
  }

  .detail_right {
    width: 50%;
    height: 0.7rem;
    text-align: center;
    float: right;
    margin-top: -0.7rem;
  }

  .detail_right p {
    font-size: 0.1rem;
    color: #666;
  }

  .detail_right p:nth-child(2) {
    line-height: 0.5rem;
  }

  .detail_right p span {
    font-size: 0.4rem;
    font-weight: bold;
  }

  .earnings {
    font-size: 0.1rem;
    color: #666;
    text-align: center;
    margin-top: 0.5rem;
  }

  .earnings_bottom {
    width: 100%;
    height: 1.5rem;
    text-align: center;
    margin-top: 0.4rem;
  }

  .earnings_bottom img {
    width: 0.7rem;
    height: 0.9rem;
  }
.van2{
  padding: .2rem;
  color: white;
  font-size: .4rem;
  vertical-align: -.3rem;
}
  .earnings_bottom p {
    font-size: 0.3rem;
    color: #999;
    padding-top: 0.1rem;
  }

  /*弹出框动画*/
  .kuang {
    width: 74%;
    background-color: #fff;
    text-align: center;
    position: absolute;
    left: 13%;
    top: 3.2rem;
    border-radius: .25rem .25rem;
    .van1 {
      padding-top: .2rem;
      font-size: 1.5rem;
      color: #f8cb86;
    }
    p {
      font-size: .3rem;
      color: #333;
      line-height: .4rem;
      margin-top: .3rem;
      padding-bottom: .3rem;
    }
    button {
      font-size: .4rem;
      color: #fff;
      font-weight: 700;
      margin-top: .4rem;
      background-color: #4cd964;
      width: 100%;
      line-height: .9rem;
      border-radius: 0 0 .25rem .25rem;
    }
  }
</style>



