<template>
  <div class="w-header clear">
    <!--左侧logo部分-->
    <div class="left">
      <router-link :to="{path:'/'}">ele.me</router-link>
    </div>
    <!--右侧logo部分-->
    <div @click="tologinpage" class="right" v-show="!isrightopc">
      <span>登录</span>
      <span>|</span>
      <span>注册</span>
    </div>
    <div @click="toprofilepage" class="pro" v-show="isrightopc">
      <i class="iconfont">&#xe63f;</i>
    </div>
  </div>
</template>

<script>
  export default {
    name: "indexheader",
    data() {
      return {
        msg: "密码登录",
        isrightopc:false,
      }
    },
    methods: {
      tologinpage() {
        this.$router.push({path: "/loginpage", query: {msg: "密码登录"}})
      },
      toprofilepage() {
        this.$router.push({path: "/profilepage"})
      }
    },
    mounted(){
      let rightopc = this.$store.state.isrightopc;
      if (rightopc) { // vuex获取到
        this.isrightopc = rightopc;
      } else { //vuex没有获取到 调localstorage
        let localisrightopc = JSON.parse(localStorage.getItem('localisrightopc'));
        if (localisrightopc) {
          this.isrightopc = localisrightopc;
        }
        else { // localstorage没有获取到用户信息
          // 请重新登录
          // this.isrightopc = true
          console.log('重新登录');
        }
      }
    },
    created(){
      localStorage.setItem('localisrightopc', JSON.stringify(this.isrightopc))

    }

  }
</script>

<style scoped lang="less">
  /*头部css样式*/
  .w-header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    line-height: 1rem;
    height: 1rem;
    font-size: 0.3rem;
    background: #3190e8;
    padding: 0 0.2rem;
    /*左侧样式*/
    a {
      color: white;
    }
    /*右侧样式*/
    .right {
      position: relative;
      span {
        color: white;
      }
    }
    .pro {
      position: absolute;
      top: 0;
      right: 0.4rem;
      i {
        color: white;
        font-size: 0.5rem;
      }
    }

  }
</style>



