<template>
  <!--网页部分-->
  <div class="indexapp">
    <indexheader></indexheader>
    <!--上部列表部分-->
    <ul class="ultop">
      <li class="litop clear">
        <span class="left">当前定位城市:</span>
        <span class="right">定位不准确时,请在城市列表中选择</span>
      </li>

      <li @click="gologiccity(guessCity)" class="libottom clear">
        <span class="left">{{guessCity.name}}</span>
        <van-icon name="arrow" class="right"></van-icon>
        <!--<span class="right">></span>-->
      </li>
    </ul>
    <!--中部列表部分-->
    <ul class="ulmiddle">
      <li class="li_m_t">
        <span>热门城市</span>
      </li>
      <!--循环热门城市-->
      <li class="li_m_m">
        <a v-for="h in hotCity" @click="gologiccity(h)">{{h.name}}</a>
      </li>
    </ul>
    <!--下部列表部分-->
    <div class="ulbottom">
      <!--循环城市按字母排序部分-->
      <ul class="li_b_t" v-for="(Obj,k) in sortObjByKey(groupCity)">
        <span>{{k}}</span>
        <li class="li_b">
          <!--循环所有城市部分-->
          <a v-for="(v,key) in Obj" @click="gologiccity(v)">{{v.name}}</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  //引入http封装模块
  import {get} from '../config/http'
  import Indexheader from "./indexheader";

  export default {
    name: "index",
    components: {Indexheader},
    // 数据部分
    data() {
      return {
        guessCity: "",//定位城市数据
        hotCity: [],//热门城市数据
        groupCity: [],//所有城市数据
        rightopc:""

      }
    },
    // 函数部分
    methods: {
      // 所有城市按英文字母排序函数
      sortObjByKey(groupCity) {
        let keys = Object.keys(groupCity).sort();
        let newObj = {};
        for (let i = 0; i < keys.length; i++) {
          let index = keys[i];
          newObj[index] = groupCity[index];
        }
        return newObj
      },
      //点击定位城市跳转
      gologiccity(v) {
        this.$router.push({path: '/citypage', query: {city: v.name, cityid: v.id}})
      }
    },
    // 钩子函数
    mounted() {
      // get请求获取定位城市数据
      get('/v1/cities', {
        type: 'guess'
      }).then((guessCity) => {
        this.guessCity = guessCity
      }).catch((error) => {
        console.log(error);
      })
      // get请求获取热门城市数据
      get('/v1/cities', {
        type: 'hot'
      }).then((hotCity) => {
        this.hotCity = hotCity;
      }).catch((error) => {
        console.log(error);
      })
      // get请求获取所有城市数据
      get('/v1/cities', {
        type: 'group'
      }).then((groupCity) => {
        this.groupCity = groupCity;
      }).catch((error) => {
        console.log(error);
      })
      //从vuex中取数据isrightopc
      // let isrightopc = this.$store.state.isrightopc;
      // if (isrightopc) { // vuex获取到
      //   this.rightopc = isrightopc;
      // } else { //vuex没有获取到 调localstorage
      //   let localisrightopc = JSON.parse(localStorage.getItem('localisrightopc'));
      //   if (localisrightopc) {
      //     this.rightopc = localisrightopc;
      //   }
      //   else { // localstorage没有获取到用户信息
      //     // 请重新登录
      //     this.isrightopc = true
      //     console.log('重新登录');
      //   }
      // }
    }


  };
</script>

<style scoped lang="less">
  /*城市列表首页*/
  .indexapp {
    /*城市列表上部表格*/
    .ultop {
      margin-top: 1rem;
      line-height: 0.8rem;
      font-size: 0.2rem;
      .litop {
        border-bottom: 0.2px solid #dedede;
        padding: 0 0.2rem;
        .right {
          color: #8b8b8b;
        }
      }
      .libottom {
        border-bottom: 1.7px solid #d0d0d0;
        padding: 0 0.2rem;
        height: 0.7rem;
        line-height: 0.7rem;
        .left {
          color: #2d65ff;
          font-size: 0.3rem;
        }
        .right {
          font-size: 0.3rem;
          padding-top: 0.2rem;
          color: #999;
        }
      }
    }
    /*城市列表中部表格*/
    .ulmiddle {
      margin-top: 0.19rem;
      /*line-height: 0.4rem;*/
      font-size: 0.2rem;
      border-top: 1.7px solid #d0d0d0;
      .li_m_t {
        line-height: 0.55rem;
        border-bottom: 0.2px solid #dedede;
        span {
          padding: 0 0.2rem;
        }
      }
      .li_m_m {
        font-size: 0;
        /*line-height: 0.8rem;*/
        border-bottom: 0.2px solid #dedede;
        /*padding: 0.4rem;*/
        a {
          margin: 0;
          line-height: 0.7rem;
          text-align: center;
          color: #4993ff;
          font-size: 0.25rem;
          display: inline-block;
          width: 25%;
          border-bottom: 0.2px solid #dedede;
          border-right: 0.2px solid #dedede;
          &:nth-child(4) {
            border-right: 0;
          }
          &:nth-child(8) {
            border-right: 0;
          }
        }
      }
    }
    /*城市列表低部表格*/
    .ulbottom {
      margin-top: 0.2rem;
      /*line-height: 0.4rem;*/
      font-size: 0.22rem;
      border-top: 1.7px solid #d0d0d0;
      .li_b_t {
        line-height: 0.58rem;
        border-bottom: 0.2px solid #dedede;
        span {
          padding: 0 0.2rem;
          &:nth-child(2) {
            padding: 0;
            font-size: 0.5rem;
            color: #848484;
          }
        }
      }
      .li_b {
        font-size: 0;
        line-height: 0;
        border-bottom: 0.2px solid #dedede;
        a {
          margin: 0;
          text-align: center;
          color: #4993ff;
          font-size: 0.25rem;
          display: inline-block;
          width: 25%;
          line-height: 0.7rem;
          padding: 0 0.02rem;
          border: 0.2px solid #dedede;
          border-left: 0;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          color: black;

        }
      }
    }
  }

</style>



