<template>
  <div class="searchpage">
    <logicheader v-bind:title="msg"></logicheader>
    <div class="top">
      <input v-model="inputval" type="text" placeholder="请输入商家或美食名称">
      <i class="iconfont deletebtn" @click="deketeinputval" :style="{opacity:isdeleBtnOpc}">&#xe604;</i>
      <button @click="getshopinfor">提交</button>
    </div>
    <!--商家信息-->
    <div class="shopinfor" v-model="tabletitle">{{tabletitle}}</div>
    <!--商家信息历史记录列表-->
    <div class="shopinforhoustory">
      <li class="clearfix" v-for="val in shopinfor">
        <img class="img left" :src="`http://elm.cangdu.org/img/${val.image_path}`" alt="">
        <!--<div class="li_right left">-->
          <!--<p>-->
            <!--<span>{{val.name}}</span>-->
            <!--<span>支付</span>-->
          <!--</p>-->
          <!--<p>月售 {{val.recent_order_num}} 单</p>-->
          <!--<p>{{val.float_minimum_order_amount}} 元起送 / 距离{{val.distance}}</p>-->
        <!--</div>-->
      </li>
    </div>

    <childfooter v-bind:tomsg="msg"></childfooter>
  </div>
</template>

<script>

  import Vue from "vue"
  import Logicheader from "./logicheader";
  import Childfooter from "./goodspage/common/childfooter";

  export default {
    name: "searchpage",
    components: {Childfooter, Logicheader},
    data() {
      return {
        msg: "搜索",//底部按钮颜色
        inputval: "",//地址输入框内容
        isdeleBtnOpc: 0,//地址栏删除按钮显示隐藏
        geohash: {},//商店经纬度信息
        shopinfor: "",//搜索商家信息结果
        tabletitle: "",//表头显示内容

      }
    },
    mounted() {
      //从vuex中取数据localgeohash
      let vuexgeohash = this.$store.state.localgeohash;
      this.geohash = vuexgeohash
    },
    methods: {
      //提交输入信息搜索商家
      getshopinfor() {
        this.tabletitle = "商家"
        Vue.axios.get("/v4/restaurants", {
          params: {
            geohash: this.geohash,
            keyword: this.inputval,
          }
        }).then((result) => {
          // 搜索信息结果（数组）
          this.shopinfor = result.data;
          // console.log(this.shopinfor);
        }).catch((error) => {
          console.log(error);
        })
      },

      // 按钮删除事件
      deketeinputval() {
        this.tabletitle = "搜索历史"
        this.shopinfor = ""
        this.inputval = ""
        this.isdeleBtnOpc = 0
      }
    },
    created() {
      localStorage.tomsg = '搜索'
    },
    // 监听输入框事件
    watch: {
      inputval(inputval) {
        if (inputval.length === 0) {
          this.isdeleBtnOpc = 0
        } else {
          this.isdeleBtnOpc = 1;
        }
      }
    },

  }
</script>

<style scoped lang="less">
  .top {
    width: 100%;
    padding: .2rem;
    background-color: white;
    display: inline-block;
    margin-top: 1rem;
    input {
      width: 80%;
      border: .01rem solid #ddd;
      background-color: #f2f2f2;
      padding: .15rem;
      font-size: .31rem;
      border-radius: .1rem;
      font-weight: bold;
    }
    .deletebtn {
      font-size: 0.35rem;
      font-weight: bold;
      color: #4a6fff;
      position: absolute;
      top: 1.43rem;
      right: 1.8rem;
      background: transparent;
      opacity: 0;

    }
    button {
      width: 1.3rem;
      background: #3199e8;
      font-size: .34rem;
      color: #fff;
      line-height: .7rem;
      border-radius: .1rem;
    }
  }

  /*商家信息*/
  .shopinfor {
    font-size: .3rem;
    padding: 0.3rem 0.2rem 0.3rem 0.2rem;
  }

  /*商家信息历史记录列表*/
  .shopinforhoustory {
    li {
      padding: .2rem;
      background: white;
      border-bottom: 1px solid #e4e4e4;
      .img {
        display: inline-block;
        width: 0.7rem;
        height: 0.7rem;
        margin-right: 0.1rem;
      }
      .li_right {
        display: inline-block;
        p {
          margin: 0 auto .2rem;
          font-size: .25rem;
          &:nth-child(1) {
            span {
              &:nth-child(2) {
                color: rgb(255, 96, 0);
                border: 1px solid rgb(255, 96, 0);
                border-bottom: 2px solid rgb(255, 96, 0);
              }
            }
          }
        }
      }
    }
  }

</style>



