<template>
   <div class="mine">
     <my-header></my-header>
     <!--地址信息-->
     <ul>
       <li>
         <van-icon name="checked" class="van"/>
         <p class="p1">
           <span class="span1">王</span>
           <span>先生</span>
           <span>13123323224</span>
         </p>
         <!---->
         <p>
           <span class="span2">家</span>
           <span class="span3">6号楼</span>
         </p>

       </li>
     </ul>
     <!--新增收货地址-->
     <div class="bottom">
       <router-link :to="{path:'/newaddress',query:{name:'添加地址'}}">
         <van-icon name="add-o" class="van"/>
         <span>新增收货地址</span>
       </router-link>
     </div>
   </div>
</template>

<script>
  import MyHeader from "../mine-2/myHeader";
    export default {
        name: "chooseaddress",
      components:{MyHeader}
    }
</script>

<style scoped lang="less">
.mine{
  ul{
    display: inline-block;
    margin-top: 1.2rem;
    li>p{
      padding: .1rem 0 .1rem .7rem;
    }
    li>.p1{
      margin-top: -.8rem;
      .span1{
        font-weight: bold;
      }
    }

    li>.van{
      color: #4cd964;
      font-size: .4rem;
      padding: .2rem;
    }
    .span2{
      font-size: .25rem;
      color: #fff;
      border-radius: .1rem;
      background-color: #ff5722;
      padding: .02rem .1rem .01rem .1rem;
    }
    .span3{
      color: #777;
      font-size: .3rem;
    }
  }
  .bottom{
    text-align: center;
    position: absolute;
    bottom: 0;
    right: 0;
    left:0;
    padding: .4rem;
    a>span,.van{
      color: #3190e8;
    }
    span{
      font-size:.35rem ;
    }
    .van{
      font-size:.45rem;
    }
  }
}
</style>



