<template>
  <div class="mine">
    <!--头部我的-->
    <my-header></my-header>
    <!--第一部分图片，登录注册-->
    <router-link :to="{path:'/account'}" class="first">
      <img :src="'http://elm.cangdu.org/img/'+userinfor.avatar" alt="" class="img1">
      <span >{{allInfro.username}}</span>
      <img src="" alt="">
      <p>暂无绑定手机号</p>
      <van-icon name="arrow" class="right"/>
    </router-link>
    <!--第二部分余额 优惠，积分-->
    <div class="second">
      <ul class="clearfix">
        <!---->
        <router-link :to="{path:'/money', query:{name:'我的余额'}}">
          <li class="left">
            <p class="p1 p2">0.00</p>
            <span class="left">元</span>
            <p class="p5">我的余额</p>
          </li>
        </router-link>
        <!---->
        <router-link :to="{path:'/cheap',query:{name:'我的优惠'}}">
          <li class="left">
            <p class="p1 p3">{{userinfor.gift_amount}}</p>
            <span class="left span1">个</span>
            <p class="p5">我的优惠</p>
          </li>
        </router-link>
        <!---->
        <router-link :to="{path:'/point',query:{name:'我的积分'}}">
          <li class="left left2">
            <p class="p1 p4">0</p>
            <span class="left span1">分</span>
            <p class="p5">我的积分</p>
          </li>
        </router-link>
      </ul>
    </div>
    <!--第三部分-->
    <div class="third">
      <div class="third1">
        <router-link :to="{path:'/orderpage'}" class="third2">
          <img src="" alt="">
          <span>我的订单</span>
          <van-icon name="arrow" class="right"/>
        </router-link>
        <router-link :to="{}" class="third2">
          <img src="" alt="">
          <span>积分商城</span>
          <van-icon name="arrow" class="right"/>
        </router-link>
        <router-link :to="{path:'/card',query:{name:'会员中心'}}" class="third2">
          <img src="" alt="">
          <span>饿了么会员卡</span>
          <van-icon name="arrow" class="right"/>
        </router-link>
      </div>
      <!---->
      <div class="third3">
        <router-link :to="{path:'/service',query:{name:'服务中心'}}">
          <img src="" alt="">
          <span>服务中心</span>
          <van-icon name="arrow" class="right"/>
        </router-link>
        <router-link :to="{path:'/loadelm',query:{name:'下载'}}">
          <img src="" alt="">
          <span>下载饿了么APP</span>
          <van-icon name="arrow" class="right"/>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
  import MyHeader from "./mine-2/myHeader";
  import Vue from "vue"
  export default {
    name: "profilepage",
    components: {MyHeader},
    data(){
      return{
        allInfro:"",
        userinfor:"",
      }
    },
    created(){
      // 定义变量userInfo，把vuex中的数据赋值给userInfo
      let userInfo = this.$store.state.userStatus;
      if (userInfo.username) { // vuex获取到
        this.allInfro = userInfo;
        // console.log('Vuex', userInfo);
      }else{ //vuex没有获取到 调localstorage
        let localData = JSON.parse(localStorage.getItem('localdata'));
        if (localData) { // localstorage获取到用户信息
          this.allInfro = localData;
          // console.log('localStorage', localStorage);
        }else{ // localstorage没有获取到用户信息
          // 请重新登录
          console.log('重新登录');
        }
      }
    //获取用户信息
      Vue.axios.get("/v1/user").then((result)=>{
        console.log(result.data);
        this.userinfor=result.data;
      }).catch((error)=>{
        console.log(error);
      })
    },
    methods: {
      //退出登录
      loginOut() {
        this.$store.commit('saveUserStatus', null); // 清除Vuex
        localStorage.removeItem('localdata');  // 清除localStorage
      },
    },
  }
</script>

<style scoped lang="less">
  .mine {
    width: 100%;
    height: 80%;
    background: rgba(0, 0, 0, 0.02);
    .first {
      display: inline-block;
      width: 100%;
      height: 1.95rem;
      background: #3190e8;
      position: relative;
      margin-top: .8rem;
      .img1 {
        display: inline-block;
        width: 1.3rem;
        height: 1.3rem;
        border-radius: 50%;
        vertical-align: middle;
        margin-top: .4rem;
        margin-left: .4rem;
      }
      span {
        font-weight: 600;
        font-size: .45rem;
        color: #fff;
        padding-left: .1rem;
      }
      p {
        font-size: .3rem;
        color: #fff;
        position: absolute;
        left: 2.4rem;
        top: 1.2rem;
        font-weight: 400;
      }
      .right {
        padding: .8rem .3rem .5rem;
        font-weight: 200;
        color: #fff;
      }
    }
    .second {
      width: 100%;
      text-align: center;
      padding-bottom: .2rem;
      li {
        width: 33.3%;
        border-right: .25px solid #e4e4e4;
        background: #fbfbfb;
        span {
          display: inline-block;
          margin-left: 1.9rem;
          margin-top: -.6rem;
        }
        .span1 {
          margin-left: 1.45rem;
        }
        .p1 {
          font-size: .6rem;
          font-weight: 700;
          padding-top: .4rem;
          padding-bottom: .3rem;
        }
        .p2 {
          color: #f90;
        }
        .p3 {
          color: #ff5f3e;
        }
        .p4 {
          color: #6ac20b;
        }
        .p5 {
          font-size: .3rem;
          color: #666;
          font-weight: 400;
          padding-bottom: .3rem;
        }
      }
      .left2 {
        border-right: none;
      }
    }
    .third {
      width: 100%;
      .third1 {
        padding-bottom: .2rem;
        .third2 {
          background-color: #fbfbfb;
        }
      }
      a {
        display: block;
        line-height: 1rem;
        font-size: .35rem;
        border-bottom: .25px solid #e4e4e4;
        padding-left: .8rem;
        .right {
          padding-right: .2rem;
          padding-top: .3rem;
          font-weight: 200;
          color: rgb(186, 186, 186);
        }
      }
      .third3 {
        background-color: #fbfbfb;
      }
    }
  }
</style>



