<template>
    <div class="mine">
      <my-header></my-header>
      <div class="top">
        <p>支付剩余时间</p>
        <van-count-down
          millisecond
          :time="time"
          format="HH : mm : ss"
        class="van" :auto-start="true"  @finish="finished"/>
      </div>
      <!--选择支付方式-->
      <p>选择支付方式</p>
      <!--底部支付宝，微信选择-->
      <div class="bottom">
        <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1565417215483&di=6132da8afec2273fdcfe874f8971c038&imgtype=0&src=http%3A%2F%2Fwww.sj520.cn%2Fsc%2Fima%2Fzifubao_sj520_10.jpg" alt="">
        <span>支付宝</span>
        <van-icon name="checked" class="van1" @click="money" :style="{'color':color}"/>
        <!--微信-->
        <img src="http://pic93.nipic.com/file/20160325/11778999_104847144671_2.jpg" alt="">
        <span>微信</span>
        <van-icon name="checked" class="van2" @click="money1" :style="{'color':color1}"/>
      </div>
      <!--button按钮-->
      <button class="btn1" @click="confirmpay">确认支付</button>
      <!--进入页面弹出的框动画-->
      <transition enter-active-class="animated bounceIn " leave-active-class="opacity 0">
        <div class="kuang" v-if="show">
          <van-icon name="warning-o"  class="van1"/>
          <p>{{words}}</p>
          <button @click="confirm">确认</button>
        </div>
      </transition>

      <!--点击确认支付弹出的框动画-->
      <transition enter-active-class="animated bounceIn " leave-active-class="opacity 0">
        <div class="kuang" v-if="show1">
          <van-icon name="warning-o"  class="van1"/>
          <p>{{words}}</p>
          <button @click="confirm1">确认</button>
        </div>
      </transition>
    </div>
</template>

<script>
  import MyHeader from "./myHeader";
    export default {
        name: "pay",
      components: {MyHeader},
      data(){
          return{
            show:true,
            time:15*60*1000,
            words:"暂不开放支付功能",
            show1:false,
            color:"#4cd964",
            color1:"#ccc",
          }
      },
      methods:{
          confirm(){
            this.show=false;
          },
        finished(){
            this.show=true;
            this.words='倒计时结束';
        },
        confirmpay(){
          this.show1=true;
          this.words='当前环境无法支付，请打开官方APP进行支付';

        },
        confirm1(){
          this.show1=true;
          this.$router.push({path:'/profilepage'});
        },
        money(){
          this.color="#4cd964";
          this.color1="#ccc";
        },
        money1(){
          this.color="#ccc";
          this.color1="#4cd964";
        }
      }
    }
</script>

<style scoped lang="less">
.mine{
  background-color: #f2f2f2;
  .top{
    display: inline-block;
    width: 100%;
    margin-top: 1rem;
    background-color: white;
    text-align: center;
    p{
      padding-top: .7rem;
      font-size: .3rem;
      color: #666;
    }
    .van{
      font-size: .7rem;
      padding: .3rem 0 .7rem;
    }
  }
  p{
    color: #666;
    padding: .3rem;
  }
  .bottom{
    background-color: white;
    border-bottom: .01rem solid #F5F5F5;
    img{
      width: 1rem;
      height: 1rem;
      padding: .2rem 0 .2rem .2rem;
    }
    span{
      color: #666;
      vertical-align: .5rem;
    }
    .van1{
      font-size:.55rem;
      color: #4cd964;
      vertical-align: .3rem;
      padding:.2rem .2rem  .2rem 4.3rem;
    }
    .van2{
      font-size:.55rem;
      color: #ccc;
      vertical-align: .3rem;
      padding:.2rem .2rem  .2rem 4.6rem;
    }
  }
  .btn1{
    width: 96%;
    background-color: #4cd964;
    font-size: .33rem;
    color: #fff;
    text-align: center;
    line-height: .9rem;
    border-radius: .1rem;
    margin: .3rem 2% .3rem 2%;
    font-weight: 700;
  }
  .kuang{
    width: 74%;
    background-color: #fff;
    text-align: center;
    position: absolute;
    left: 13%;
    top: 3.2rem;
    border-radius:.25rem .25rem  ;
    .van1{
      padding-top: .2rem;
      font-size: 1.5rem;
      color: #f8cb86;
    }
    p{
      font-size: .3rem;
      color: #333;
      line-height: .4rem;
      margin-top: .3rem;
      padding-bottom: .3rem;
    }
    button{
      font-size: .4rem;
      color: #fff;
      font-weight: 700;
      margin-top: .4rem;
      background-color: #4cd964;
      width: 100%;
      line-height: .9rem;
      border-radius: 0 0 .25rem .25rem;
    }
  }
}
</style>



