<template>
  <div>
    <div class="win">
      <i class="iconfont" @click="tosearchpage">&#xe607;</i>
      <span>{{storename}}</span>
      <p>
        <i class="iconfont" @click="toprofilepage">&#xe63f;</i>
      </p>
    </div>
  </div>
</template>

<script>
  export default {
    name: "childheader",
    data() {
      return {}
    },
    props: {
      storename: {
        type: String
      }
    },
    methods: {
      tosearchpage() {
        this.$router.push({path: "/searchpage"})
      },
      toprofilepage() {
        this.$router.push({path: "/account"})
      }
    },
  }
</script>

<style scoped>
  .win {
    width: 100%;
    height: 1rem;
    background-color: rgb(49, 144, 232);
    padding: 0.02rem 0px 0 0.04rem;
    position: fixed;
    text-align: center;
    left: 0;
    top: 0;
    z-index: 2;
  }

  .iconfont {
    color: white;
    line-height: 1rem;
    font-size: 0.4rem;
    /*border: 1px solid red;*/
    /*margin-top: 1rem;*/
  }

  .win i {
    /*左搜索按钮 */
    font-size: 0.6rem;
    float: left;
    margin-left: 0.1rem;
  }

  /*标题文字*/
  .win span {
    color: white;
    display: inline-block;
    width: 3.8rem;
    font-size: 0.38rem;
    line-height: 0.93rem;
    position: absolute;
    left: 1.9rem;
    /*超出文字以省略号表示*/
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  .win > p > i {
    /*左侧个人中心按钮*/
    height: 0.8rem;
    font-size: 0.6rem;
    color: rgb(233, 243, 253);
    padding: 0;
    position: absolute;
    right: 0.2rem;
    top: 0rem;
  }
</style>



