// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
//axios发起请求
import axios from 'axios'
import VueAxios from 'vue-axios'
// 重置样式
import "./assets/css/reset.css"
// 监听屏幕
import "./assets/js/resize"
//引入UI框架
import Vant, {Popup} from 'vant';
import 'vant/lib/index.css';
// elementUI
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
//引入animated动画样式
import "../node_modules/animate.css"

import "./style/common.less"
//配置vuex+localStorage所需的配置
import createPersistedState from 'vuex-persistedstate'
// 引入vuex的store模块
import "./store/vuexindex"
//填入vuex模块
import Vuex from 'vuex';

Vue.use(Popup);

Vue.use(ElementUI)

Vue.use(VueAxios, axios);
Vue.use(Vant);
Vue.use(Vuex);

const store = new Vuex.Store({
//配置vuex+localStorage所需的配置
  plugins: [createPersistedState()],

  state: {
    //数据
    userStatus: {},//用户信息数据
    isrightopc: "",//用户登录状态
    localgeohash: {},//储存的经纬度信息
    lacalshoplatitude: "",//商店的纬度信息
    lacalshoplongitude: "",//商店的经度信息
    localname: "",//轮播图分类名字
    address: {},//添加新地址
    addressname: '',//添加新地址用户名
    shopinfor: "",//商家信息
    orderlistinfor: "",//返回的订单列表信息
    allgoodsinfor:"",//商品总数据信息
  },
  mutations: {
    //操作state里的数据
    // 储存用户信息
    saveUserStatus(state, payload) {
      state.userStatus = payload;
    },
    // 储存用户登录状态
    saveisrightopc(state, payload) {
      state.isrightopc = payload;
    },
    //储存商户的经纬度信息
    savelocalgeohash(state, payload) {
      state.localgeohash = payload;
    },
    //储存轮播图分类名字
    saveloaclname(state, payload) {
      state.localname = payload
    },
    //储存商户的纬度信息
    savelacalshoplatitude(state, payload) {
      state.lacalshoplatitude = payload;
    },
    //储存商户的纬度信息
    savelacalshoplongitude(state, payload) {
      state.lacalshoplongitude = payload;
    },
    //添加新地址方法
    saveAddress(state, payload) {
      state.address = payload;
    },
    //添加新地址方法
    saveAddressname(state, payload) {
      state.addressname = payload;
    },
    //点击具体的商家信息
    saveshopinfor(state, payload) {
      state.shopinfor = payload
    },
    //返回的订单列表信息
    saveorderlistinfor(state, payload) {
      state.orderlistinfor = payload
    },
    //商品总数据信息
    saveallgoodsinfor(state, payload) {
      state.allgoodsinfor = payload
    },
  }
});
// import "./config/rem";

Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: {App},
  template: '<App/>'
});



