<template>
  <div class="bottom">
    <router-link :to="{path:'/waimaipage'}">
      <div class="c1" @click="towaimaipage('外卖')">
        <i class="iconfont" :class="{bgc:tomsg==='外卖'}"> &#xe60d;</i>
        <p :class="{bgc:tomsg==='外卖'}">外卖</p>
      </div>
    </router-link>
    <router-link :to="{path:'/searchpage'}">
      <div class="c2" @click="towaimaipage('搜索')">
        <i class="iconfont" :class="{bgc:tomsg==='搜索'}">&#xe60c;</i>
        <p :class="{bgc:tomsg==='搜索'}">搜索</p>
      </div>
    </router-link>
    <router-link :to="{path:'/orderpage'}">
      <div class="c3" @click="towaimaipage('订单')">
        <i class="iconfont" :class="{bgc:tomsg==='订单'}">&#xe738;</i>
        <p :class="{bgc:tomsg==='订单'}">订单</p>
      </div>
    </router-link>
    <router-link :to="{path:'/profilepage'}">
      <div class="c4" @click="towaimaipage('我的')">
        <i class="iconfont" :class="{bgc:tomsg==='我的'}">&#xe600;</i>
        <p :class="{bgc:tomsg==='我的'}">我的</p>
      </div>
    </router-link>


  </div>
</template>

<script>
  export default {
    name: "childfooter",
    data() {
      return {
        tomsg: '',
      }
    },
    created() {
      this.tomsg = localStorage.tomsg
    },
    methods: {
      towaimaipage(v) {
        localStorage.tomsg = v;
        this.tomsg = v;
      },
    },

  }
</script>

<style scoped>
  .bgc {
    color: #4775ee;
  }
  a{
    width: 25%;
  }

  .bottom {
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: white;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 0.2rem 0;
    text-align: center;
  }

  .bottom p {
    font-size: 0.1rem;
  }


  .iconfont {
    font-size: 0.5rem;
  }


</style>



