    <transition enter-active-class="animated fadeIn" leave-active-class="opacity 0">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>

  import 'vant/lib/icon/local.css';

  export default {
    name: 'App',

  }
</script>

<style>
  * {
    padding: 0;
    margin: 0;
  }

  body, html {
    width: 100%;
    height: 100%;
  }

  #app {
    width: 100%;
    height: 100%;
  }

  /*定义字体图标*/
  @font-face {
    font-family: 'iconfont';  /* project id 1330864 */
    src: url('//at.alicdn.com/t/font_1330864_7a0vbjj9nbi.eot');
    src: url('//at.alicdn.com/t/font_1330864_7a0vbjj9nbi.eot?#iefix') format('embedded-opentype'),
    url('//at.alicdn.com/t/font_1330864_7a0vbjj9nbi.woff2') format('woff2'),
    url('//at.alicdn.com/t/font_1330864_7a0vbjj9nbi.woff') format('woff'),
    url('//at.alicdn.com/t/font_1330864_7a0vbjj9nbi.ttf') format('truetype'),
    url('//at.alicdn.com/t/font_1330864_7a0vbjj9nbi.svg#iconfont') format('svg');
  }

  .iconfont {
    font-family: "iconfont" !important;
    font-size: 16px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
  }

</style>



