<template>
  <div class="login">
    <!--引入头组件-->
    <logicheader v-bind:title="msg"></logicheader>
    <!--上部组件部分-->
    <div class="logintop">
      <input v-model="username" class="username" type="text" placeholder="账号">
      <input v-model="password" class="password" type="password" placeholder="密码">
      <van-switch v-model="checked" size="22px"/>
      <input v-model="captcha_code" class="code" type="text" placeholder="验证码">
      <div class="codebtn">
        <img :src=this.imgsrc alt="">
        <span>看不清</span>
        <button @click="nextimage" class="nextimg">换一张</button>
      </div>
    </div>
    <!--下部组件部分-->
    <div class="loginbuttom">
      <p>
        温馨提示：未注册过的账号，登录时将自动注册
      </p>
      <p>
        注册过的用户可凭账号密码登录
      </p>
      <button @click="submitbtn" class="loginbtn">登录</button>
      <!--弹框部分-->
      <transition enter-active-class="animated bounceIn" leave-active-class="opacity 0">
        <div class="kuang" v-if="show">
          <van-icon name="warning-o" class="van1"/>
          <p v-model="kuangmsg">{{kuangmsg}}</p>
          <!--<p>请输入手机号/邮箱/用户名</p>-->
          <button @click="confirm">确认</button>
        </div>
      </transition>
      <router-link :to="{path:'/forgetpage',query:'重置密码'}">重置密码?</router-link>
    </div>

  </div>
</template>

<script>
  // js部分
  import Logicheader from "./logicheader";
  import Vue from 'vue'

  export default {
    name: "loginpage",
    components: {Logicheader},
    data() {
      return {
        checked: false,
        msg: "密码登录",
        imgsrc: "",
        username: "",
        password: "",
        captcha_code: "",
        show: false,
        kuangmsg: "",
        allinfor: "",
        isrightopc: true
        ,
      }
    },
    methods: {
      confirm() {
        this.show = false;
      },
      // onInput(checked) {
      //   Dialog.confirm({
      //     title: '提醒',
      //     message: '是否切换开关？'
      //   }).then(() => {
      //     this.checked = checked;
      //   });
      // },


      // 点击换一张验证码
      nextimage() {
        Vue.axios.post("/v1/captchas", {}).then((result) => {
          this.imgsrc = result.data.code
        })
        console.log(this.imgsrc);
      },

      // 点击提交账号,密码及验证码
      submitbtn() {
        // 登录注册
        Vue.axios.post("/v2/login", {
          username: this.username,
          password: this.password,
          captcha_code: this.captcha_code
        }).then((res) => {
          console.log(res.data);
          if (res.data.message == "验证码不正确") {
            this.show = true;
            this.kuangmsg = "验证码不正确";
            this.nextimage();
          } else if (res.data.message == "密码参数错误") {
            this.show = true;
            this.kuangmsg = "请输入密码"
            this.nextimage();
          } else if (res.data.message == "验证码参数错误") {
            this.show = true;
            this.kuangmsg = "请输入验证码"
            this.nextimage();
          } else if (res.data.message == "用户名参数错误") {
            this.show = true;
            this.kuangmsg = "请输入手机号/邮箱/用户名"
            this.nextimage();
          } else if (res.data.message == "密码错误") {
            this.show = true;
            this.kuangmsg = "密码错误"
            this.nextimage();
          } else {
            // 登陆成功跳转路由
            this.$router.push({path: "/index"});
            // 将登陆成功状态储存在vuex中
            this.$store.commit('saveisrightopc',this.isrightopc)
            // // 将用户登录状态储存在localstorage中
            // localStorage.setItem('localisrightopc', JSON.stringify(this.isrightopc))

            this.allinfor = res.data
            // 将用户信息储存在vuex中
            this.$store.commit('saveUserStatus', this.allinfor);
            // // 将用户登储存在localstorage中
            // localStorage.setItem('localdata', JSON.stringify(this.allinfor))
          }
        })
      },
    },

    mounted() {
      // // 接收首页头部的传值
      // this.fromindex = this.$route.query.msg;
      // 请求获取验证码图片
      this.nextimage();

    },

  }
</script>


<style scoped lang="less">
  /*css样式*/
  .login {
    width: 100%;
    .logintop {
      display: inline-block;
      position: relative;
      margin-top: 1.05rem;
      input {
        width: 100%;
        font-size: 0.28rem;
        line-height: 0.46rem;
        padding: 0.2rem;
        border-bottom: 1px solid #f1f1f1;
      }
      .codebtn {
        position: absolute;
        bottom: 0rem;
        right: 0.2rem;
        img {

        }
        span {
          font-size: 0.2rem;
          position: absolute;
          right: 0;
          top: 0.1rem;
          margin-left: 0.5rem;
        }
        .nextimg {
          background: transparent;
          font-size: 0.2rem;
          color: #2d65ff;
          margin: 0 0 0 0.1rem;
          padding-bottom: 0.1rem;
        }
      }
      .van-switch {
        position: absolute;
        top: 1.2rem;
        right: 0.2rem;
        height: 0.2rem;
        width: 0.7rem;
        background: #d0d0d0;
        .van-switch__node {
          position: absolute;
          top: -0.12rem;
          background: #eeeeee;
          width: .4rem;
          height: .4rem;
        }
      }
      .van-switch--on {
        background: #37e254;
        position: absolute;
        top: 1.2rem;
        height: 0.2rem;
        width: 0.7rem;
        .van-switch__node {
          position: absolute;
          top: -0.12rem;
          background: #eeeeee;
          width: .4rem;
          height: .4rem;

        }
      }
    }
    .loginbuttom {
      padding: 0.2rem 0 0 0.2rem;
      overflow: hidden;
      p {
        font-size: 0.2rem;
        padding: 0.01rem 0.01rem 0.25rem 0;
        color: red;
        &:nth-child(2) {
          padding-bottom: 0.2rem;
        }
      }
      .loginbtn {
        width: 96%;
        margin: 0 0 0.2rem;
        font-size: .3rem;
        color: #fff;
        background-color: #4cd964;
        padding: .24rem 0;
        border-radius: .08rem;
        text-align: center;
      }
      /*弹框样式*/
      .kuang {
        width: 74%;
        background-color: #fff;
        text-align: center;
        position: absolute;
        left: 13%;
        top: 2.8rem;
        border-radius: .25rem .25rem 0 0;
        .van1 {
          padding-top: .2rem;
          font-size: 1.5rem;
          color: #f8cb86;
        }
        p {
          font-size: .3rem;
          color: #333;
          line-height: .4rem;
          margin-top: .3rem;
          padding-bottom: .3rem;
        }
        button {
          font-size: .4rem;
          color: #fff;
          font-weight: 700;
          margin-top: .2rem;
          background-color: #4cd964;
          width: 100%;
          line-height: .9rem;
          border-radius: 0 0 .25rem .25rem;
        }
      }

      a {
        font-size: 0.25rem;
        color: #30b1ff;
        margin: 0.2rem 0.5rem 0 0;
        float: right;
      }
    }

  }


</style>



