<template>
  <div class="mine">
    <!--头部-->
    <div class="header">
      <van-icon name="arrow-left" class="van1" @click="backHtml"/>
      <span>编辑地址</span>
      <span v-show="isdeleopc" @click="opcdeletebtn" class="right right1">编辑</span>
      <span v-show="isopc" @click="findeletebtn" class="right">完成</span>
    </div>
    <!--地址栏循环部分-->
    <div class="div1"></div>
    <ul>
      <li v-for="(pro,index) in array1" :key="index">
        <van-icon name="cross" v-show="isxopc" class="right" @click="deleted"/>
        <span>{{pro.address}}</span>
        <p>{{pro.phone}}</p>
      </li>
    </ul>
    <!--新增地址-->
    <div class="bottom" @click="change">
      <span>新增地址</span>
      <van-icon name="arrow" class="right"/>
    </div>
  </div>
</template>
<script>
  import Vue from "vue"

  export default {
    name: "newaddress",
    components: {},
    data() {
      return {
        array1: [],
        localData: "",
        isopc: false,//控制删除按钮显隐
        isxopc: false,
        isdeleopc: true,
      }
    },
    methods: {
      backHtml() {
        this.$router.push({path: '/account'})
      },
      change() {
        this.$router.push({path: '/addaddress', query: {name: '新增地址'}})
      },
      //删除li
      deleted() {
        Vue.axios.delete(`v1/users/${this.localData.user_id}/addresses/${this.address}`).then((result) => {
          console.log(result.data);
        }).catch((error) => {
          console.log(error);
        })
      },
      opcdeletebtn() {
        this.isopc = true
        this.isxopc = true
        this.isdeleopc = false
      },
      findeletebtn() {
        this.isopc = false
        this.isxopc = false
        this.isdeleopc = true
      },
    },
    created() {
      //   从localStorage中把用户信息取出来
      this.localData = this.$store.state.userStatus;
      console.log(this.localData.user_id);
      // this.isopc = "1";
      //向后台发起请求拿到地址信息
      Vue.axios.get(`v1/users/${this.localData.user_id}/addresses`).then((result) => {
        // console.log(result.data);
        this.array1 = result.data;
      }).catch((error) => {
        console.log(error);
      })
    }
  }
</script>

<style scoped lang="less">
  .mine {
    margin-top: .1rem;
    .header {
      width: 100%;
      background-color: #3190e8;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 1;
      .van1 {
        color: #fff;
        margin: .28rem;
      }
      span {
        color: #fff;
        font-size: .4rem;
        font-weight: 700;
        padding-left: 1.6rem;
      }
      .right {
        right: .2rem;
        font-size: .31rem;
        color: #fff;
        font-weight: 400;
        position: absolute;
        top: .4rem;
        right: .2rem;
      }
      .right1 {
        z-index: 1;
      }
    }
    .div1 {
      width: 100%;
      height: .9rem;
    }
    ul {
      width: 100%;
      display: inline-block;
      list-style: none;

      li {
        width: 100%;
        padding: .15rem;
        border-bottom: .5px solid #d9d9d9;
        font-size: .25rem;
        &:nth-child(1) {
          background-color: #fff8c3;
        }
        span {
          display: block;
          margin-bottom: .2rem;
        }
        .right {
          margin-top: .3rem;
        }
      }
    }
    .bottom {
      width: 100%;
      border: 1px solid #d9d9d9;
      background-color: white;
      height: .9rem;
      padding: .25rem;
      margin-top: .15rem;
      right: 0;
      .right {
        color: rgb(216 216 216);
      }
    }
  }
</style>



