<template>
  <div class="mine">
    <my-header></my-header>
    <div class="top">
      <input type="text" placeholder="输入用户名" v-model="dataname" :style="{'border':isTrue}">
      <p :style="{opacity:show}">用户名只能修改一次 （5-24字符之间）</p>
      <p class="p1" :style="{opacity:isopa}">用户名长度在5到24位之间</p>
      <button @click="confirm">确认修改</button>
    </div>


  </div>
</template>

<script>
  import MyHeader from "./myHeader";

  export default {
    name: "changeusername",
    components: {MyHeader},
    data() {
      return {
        data: "",
        isTrue: "1px solid #ddd",
        isopa: 0,
        show: 1,
        //设置button点击属性（隐藏显现）
        disabled: true,
        username: "",
        dataname: "",
      }
    },
    methods: {
      confirm() {
        this.userStatus = this.$store.state.userStatus
        this.userStatus.username = this.dataname
        //再把值存入vuex中
        this.$store.commit('saveUserStatus', this.userStatus);
        this.$router.push({path: '/account'})
      }
    },
    watch: {
      data() {
        if (this.data.length > 0 && this.data.length < 5 || this.data.length > 24) {
          this.isTrue = "1px solid #f00";
          this.isopa = 1;
          this.show = 0;
          this.disabled = true;
        } else {
          this.isTrue = "1px solid #ddd";
          this.isopa = 0;
          this.show = 1;
          this.disabled = false;
        }

      }
    },
  }
</script>

<style scoped lang="less">
  .top {
    margin-top: 1.2rem;
    display: inline-block;
    width: 100%;
    input {
      width: 96%;
      margin-left: 2%;
      background-color: #f2f2f2;
      border: 1px solid #ddd;
      padding: .1rem;
      line-height: .6rem;
      font-size: .3rem;
      color: #9d9488;
      position: relative;
    }
    p {
      font-size: .2rem;
      color: #666;
      padding: .2rem 0 .5rem .2rem;
    }
    .p1 {
      font-size: .2rem;
      color: red;
      position: absolute;
      top: 2rem;
    }
    button {
      width: 96%;
      margin-left: 2%;
      background: #3190e8;
      line-height: .85rem;
      font-size: .31rem;
      color: #fff;
      opacity: .6;
    }
  }
</style>



