          <p>综合评价</p>
          <p>高于周边商家76.9%</p>
        </section>
        <section class="se2">
          <p class="p1">
            <span>服务态度</span>
            <span style="color: #f60">★★★★★</span>
            <span style="color: #f60;">4.7</span>
          </p>
          <p class="p2">
            <span>菜品评价</span>
            <span style="color: #f60">★★★★★</span>
            <span style="color: #f60">4.8</span>
          </p>
          <p class="p3">
            <span>送达时间</span>
            <span style="font-size: 0.1rem;color: #999;">分钟</span>
          </p>
        </section>
      </header>
      <div class="Article"></div>
      <ul class="center_evaluate" @click="addressDetail">
        <li :class="{'b-Red':show,'red':showed}" class="evaluate_one">全部(473)</li>
        <li>满意(453)</li>
        <li class="evaluate_two">不满意(20)</li>
