        <li>包装精美(15)</li>
        <li>卫生干净(15)</li>
        <li>食材新鲜(15)</li>
        <li>服务不错(11)</li>
      </ul>
      <!--中间分割线-->
      <div class="wire"></div>
      <!--底部用户评价部分-->
      <div class="bottom_base" v-for="(pro,index) in array">
        <!--图片头像 因为找不到相同的 先找个代替 随时可换-->
        <!--<img class="base_img" src="../pages/img/31.jpg">-->
        <img class="base_img" src="//elm.cangdu.org/img/default.jpg">
        <!--评论名字 日期-->
        <p class="base_name">
          <span>{{pro.username}}</span>
          <span>{{pro.rated_at}}</span>
          <span>★★★★★</span>
          <span>{{pro.time_spent_desc}}</span>
        </p>
        <!--评价送达时间-->
        <p class="base_time">

        </p>
        <!--用户图片评价-->
        <p class="specimen">
          <img :src="getImgPath(p.image_hash)" v-for="(p,i) in pro.item_ratings">
          <!--<img src="">-->
        </p>
        <!--底部数字评价-->
        <p class="base_figure">
          <span>超级...</span>
          <span>韩式...</span>
        </p>
      </div>
