<script>
  import Vue from "vue";

  export default {
    name: "evaluate",
    data() {
      return {
        show: true,
        showed: false,
        array: "",
      }
    },


    created() {
      Vue.axios.get("https://elm.cangdu.org/ugc/v2/restaurants/1/ratings?offset=0&limit=10", {}).then((res) => {
        console.log(res.data);
        this.array = res.data;
      })
    },
    methods: {
      goback() {
        this.$router.go(-1)
      },
      addressDetail() {
        this.show = false;
        this.showed = true
      },
      getImgPath(path) {
        let suffix;
        if (!path) {
          return '//elm.cangdu.org/img/default.jpg'
        }
        if (path.indexOf('jpeg') !== -1) {
          suffix = '.jpeg'
        } else {
          suffix = '.png'
        }
        let url = '/' + path.substr(0, 1) + '/' + path.substr(1, 2) + '/' + path.substr(3) + suffix;
        return 'https://fuss10.elemecdn.com' + url
      },

    },


  }
</script>

