<template>
  <div class="w-header clear">
    <!--左侧部分-->
    <div class="box left">
      <van-icon @click="goback" name="arrow-left" class="arrow-left"></van-icon>
    </div>
    <!--中部部分-->
    <div class="box">
      <span>
       {{citypage}}
      </span>
    </div>
    <!--右侧部分-->
    <div class="box right">
      <span @click="goindex">切换城市</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: "cityheader",
    data() {
      return {
        guessCity: [],//点位城市信息
        city: "",//热门城市,所有城市信息
      }
    },
    // 接收index传过来的数据
    props: {
      citypage: {
        type: String,
      }
    },
    methods: {
      // 退回上一步
      goback() {
        this.$router.go(-1)
      },
      // 回到首页
      goindex() {
        this.$router.push("/")
      }
    },

  }
</script>

<style scoped lang="less">
  /*头部css样式*/
  .w-header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    width: 100%;
    line-height: 1rem;
    height: 1rem;
    font-size: 0.35rem;
    background: #3190e8;
    padding: 0 0.2rem;
    text-align: center;
    /*左侧样式*/
    .box {
      display: inline-block;
      &:nth-child(1) {
        .arrow-left {
          margin-top: 0.25rem;
          font-size: 0.5rem;
          color: #f5f5f5
        }

      }
      &:nth-child(2) {
        padding-left: 0.8rem;
        span {
          color: #f5f5f5
        }
      }
      &:nth-child(3) {
        span {
          font-size: 0.35rem;
          color: #f5f5f5
        }
      }

    }
    /*中部样式*/
    /*.middle {*/
    /*width: 20%;*/
    /*span {*/

    /*color: white;*/
    /*font-weight: bold;*/
    /*font-size: 0.8rem;*/
    /*}*/
    /*}*/
    /*右侧样式*/
    /*.right {*/
    /*display: inline-block;*/
    /*span {*/

    /*}*/

    /*}*/

  }
</style>



