<template>
  <div class="header">
    <router-link :to="{}">
      <van-icon name="arrow-left" class="van1" @click="backHtml"/>
    </router-link>
    <span>{{abc}}</span>
  </div>
</template>

<script>
    export default {
        name: "myHeader",
      data(){
          return{
            abc:"",
          }
      },
      methods:{
        backHtml(){
          this.$router.go(-1);
        }
      },
      created(){
         this.abc = this.$route.query.name
      },

    }
</script>

<style scoped lang="less">
  .header{
    width: 100%;
    height: 1rem;
    background-color: #3190e8;
    position: fixed;
    top: 0;
    left: 0;
    z-index:1;
    a{
      display: inline-block;
      margin:.3rem;
      font-size: .5rem;
      .van1{
        color: #fff;
      }
    }
    span{
      color: #fff;
      font-size: .4rem;
      font-weight: 700;
      padding-left: 1.5rem;
    }
  }
</style>



