<template>
    <div class="mine">
      <!--有三个红包即将到期，红包说明部分-->
      <div class="middle clearfix">
        <span>有</span>
        <span>{{array1.length}}</span>
        <span>个红包即将到期</span>
        <img src="data:image/png;base64," alt="">
        <router-link :to="{path:'/redpackge',query:{name:'红包说明'}}">红包说明</router-link>
      </div>
      <!--红包图片部分-->
      <div class="foot clearfix" v-for="(value,index) in array1" :key="index">
        <div class="left">
          <span>￥</span>
          <span>{{value.amount}}</span>
          <p>满{{value.sum_condition}}元可用</p>
        </div>
        <div class="right">
          <h4>{{value.name}}</h4>
          <p>{{value.end_date}}到期</p>
          <p>限收货手机号为 {{value.phone}}</p>
          <h4 class="date">{{value.description_map.validity_delta}}</h4>
        </div>
      </div>

      <!--底部限品类-->
      <div class="bottom">
        <router-link :to="{path:'/redpackgehistory',query:{name:'历史红包'}}">查看历史红包
        <van-icon name="arrow" class="van1"/>
        </router-link>
      </div>
      <!--兑换红包，推荐有奖-->
      <div class="button">
        <span @click="red">兑换红包</span>
        <span @click="prize">推荐有奖</span>
      </div>
    </div>
</template>

<script>
  import  Vue from "vue"
    export default {
        name: "money-1",
      data(){
          return{
            array1:[],
            userid:"",
          }
      },
      methods:{
          red(){
            this.$router.push({path:'/redpacket'})
          },
        prize(){
          this.$router.push({path:'/prize'})
        }
      },
      mounted(){
        Vue.axios.get(`promotion/v2/users/${this.userid.user_id}/hongbaos?limit=20&ffset=0`).then((result)=>{
          console.log(result.data);
          this.array1=result.data;
        })
      },
      created(){
        this.userid = this.$store.state.userStatus
      }
    }
</script>

<style scoped lang="less">
  .middle{
    position: relative;
    width: 100%;
    font-size: .1rem;
    padding: .3rem;
  span:nth-child(1){
    padding: .3rem 0 .5rem .1rem;
  }
  span:nth-child(2){
    color: #ff5340;
  }
  span{
    color: #666;
    font-weight: 400;
  }
  a{
    color: #3190e8;
    padding-left:3.4rem;
    float: right;
  }
  img{
    width: .3rem;
    height: .3rem;
    position: absolute;
    right: 1.5rem;
    top: .25rem;
  }
  }
  .foot{
    display: inline-block;
    background: #fff url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAIBAMAAAALs8LeAAAABGdBTUEAALGPC/xhBQAAAAFzUkdCAK7OHOkAAAAeUExURUxpcf9TQf9aS/9TQP9TQP9TQv9TQf9UQP9bSP9TQCdGiZQAAAAJdFJOUwBPEZzKPX2/HKJFz58AAAAkSURBVAjXY5gJBwzEMScHNXpCmQIMDMwQ5jQGIKgEMyeBmJIAtqcnWdDMwKsAAAAASUVORK5CYII=) repeat-x;
    background-size: .2rem .1rem;
    width: 95%;
    margin-left: 2.5%;
    margin-bottom:.2rem;
    padding: .6rem .2rem .4rem .2rem;
    font-size: 0;
  .left{
    width: 28%;
    padding-right: .2rem;
    border-right: .5px dashed #ccc;
  span{
    font-size: .4rem;
    color: #ff5340;
    font-weight: 700;
  }
  span:nth-child(2){
    font-size: .7rem;
    font-weight: 400;
  }
  p{
    font-size: .2rem;
    color: #999;
    padding-top:.1rem;
  }
  }
  .right{
    width: 70%;
    padding-left: .2rem;
  h4{
    font-size: .33rem;
    font-weight: 400;
    color: #666;
  }
  p{
    color: #999;
    font-size: .22rem;
    padding-top:.1rem ;
  }
  .date{
    padding-left: 3.5rem;
    margin-top: -1rem;
    color: #ff5340;
    font-size: .38rem;
  }
  }
  }
  .bottom{
    margin-top: .4rem;
    text-align: center;
  a,.van1{
    font-size: .25rem;
    color: #999;
  }
  }
  .button{
    background-color: white;
    margin-top: 2rem;
    padding: .31rem;
    span{
     padding-left: .8rem;
      &:nth-child(2){
        padding-left: 2.3rem;
      }
    }
  }
</style>



