<template>
    <div class="mine">
      <my-header></my-header>
      <div class="top">
        <input type="text" placeholder="请输入校区/写字楼/学校等" v-model="input1">
        <button @click="address">确认</button>
      </div>
      <!--提示-->
      <p class="p1">为了满足商家的送餐要求，建议您从列表中选择地址</p>
      <!--搜索出来的地址循环-->
      <ul v-if="show">
        <li v-for="(pro,index) in data1" :key="index" @click="changeaddress(pro)">
          <p>{{pro.name}}</p>
          <p>{{pro.address}}</p>
        </li>
      </ul>
      <!--底部小提示-->
      <div class="bottom" v-if="show1">
        <p>找不到地址？</p>
        <p>请尝试输入小区、写字楼或学校名</p>
        <p>详细地址（如门牌号）可稍后输入哦。</p>
      </div>

    </div>
</template>

<script>
  import Vue from "vue"
  import MyHeader from "./myHeader";
    export default {
        name: "searchaddress",
      components: {MyHeader},
      data(){
          return{
            input1:"",
            infor:"",
            data1:"",
            show:false,
            show1:true,
          }
      },
      methods:{
        address(){
      Vue.axios.get("/v1/pois",{
        params:{ city_id:this.infor,
                 keyword:this.input1,}
      }).then((result)=>{
        console.log(result.data);
        //this.data1为获取的后台数据
        this.data1=result.data;
        //把获取的地址存入vuex中
        // this.$store.commit('saveAddress', this.data1);
        //把获取的地址存入localStorage中
        // localStorage.setItem('address',JSON.stringify(this.data1));
        //
        if(this.data1.message=="参数错误"){
          this.show=false;
          this.show1=true;
        }else{
          this.show=true;
          this.show1=false;
        }
      }).catch((error)=>{
        console.log(error);
      })
        },
        //点击地址跳转到新增地址页面
        changeaddress(v){
          //把获取的地址存入vuex中
          this.$store.commit('saveAddress', v);
          //把获取的地址存入localStorage中
          localStorage.setItem('address',JSON.stringify(v));
          this.$router.push({path:'/addaddress',query:{name:'新增地址',oldname:v.name,oldaddress:v.address,oldgeohash:v.geohash}})
        }
      },
      created(){
        //  从localStorage中把登陆的账户信息拿出来赋值给变量infor
        this.infor = JSON.parse(localStorage.getItem('localdata'));
        console.log(this.infor);

      }
    }
</script>

<style scoped lang="less">
.top{
  width: 100%;
  padding: .2rem;
  background-color: white;
  display: inline-block;
  margin-top: 1rem;
input{
  width: 80%;
  border: .01rem solid #ddd;
  background-color: #f2f2f2;
  padding: .15rem;
  font-size: .28rem;
  border-radius: .1rem;
}
  button{
    width: 1.3rem;
    background: #3199e8;
    font-size: .34rem;
    color: #fff;
    line-height: .7rem;
    border-radius: .1rem;
  }
}
.p1{
  background: #fff6e4;
  font-size: .3rem;
  color: #ff883f;
  text-align: center;
  padding: .2rem 0 0;
}
  .bottom{
  margin-top: 2.8rem;
    text-align: center;
    p{
      padding-bottom: .3rem;
      color: #969696;
    }
  }
  ul{
    margin-top: .2rem;
    font-size:.3rem;
    li{
      border-bottom: .01rem solid #ddd;
    }
    p{
      margin: .2rem;
      color: #969696;
    }
  }
</style>



