<template>
    <div class="mine">
      <my-header></my-header>
      <p class="p1">为账户{{username.username}}购买会员</p>
      <div class="top">
        <!--会员特权 会员说明-->
        <router-link :to="{path:'/mineyouhui',query:{name:'我的优惠'}}" class="clearfix">
          <span class="left">会员特权</span>
          <span class="span1">会员说明</span>
          <van-icon name="arrow" class="right"/>
        </router-link>
        <!--减免配送费-->
        <div class="middle clearfix" >
          <div class="left">
            <img src="data:image/jpeg;base64," alt="">
          </div>
        <div class="right">
          <h4>减免配送费</h4>
          <p>每月减免30单，每日可减免3单，每单最高减4元</p>
          <p>蜂鸟专送专享</p>
        </div>
        </div>
        <!---->
        <div class="middle clearfix" >
          <div class="left">
            <img src="data:image/jpeg;base64," alt="">
          </div>
          <div class="right">
            <h4>减免配送费</h4>
            <p>每月减免30单，每日可减免3单，每单最高减4元</p>
            <p>蜂鸟专送专享</p>
          </div>
        </div>
      </div>
      <!--开通会员-->
      <div class="bottom">
        <h4>开通会员</h4>
        <p>
          <span>1个月</span>
          <span>￥20</span>
          <button @click="pay">购买</button>
        </p>
      </div>
      <!--兑换会员-->
      <div class="card">
        <router-link :to="{path:'/exchangemembers',query:{name:'兑换会员'}}" class="clearfix">
          <span class="left">兑换会员</span>
          <van-icon name="arrow" class="van1 right"/>
          <span class="right">使用卡号卡密</span>
        </router-link>
      </div>
      <!--购买记录-->
      <div class="card">
        <router-link :to="{path:'/shophistory',query:{name:'购买记录'}}" class="clearfix">
          <span class="left">购买记录</span>
          <van-icon name="arrow" class="van1 right"/>
          <span class="right">开发票</span>
        </router-link>
      </div>

    </div>
</template>

<script>
  import MyHeader from "./myHeader";
    export default {
        name: "card",
      components: {MyHeader},
      data(){
          return{
            username:"",
          }
      },
      methods:{
        pay(){
          this.$router.push({path:'/pay',query:{name:'在线支付'}})
        }
      },
      created(){
        this.username = this.$store.state.userStatus;
      }
    }
</script>

<style scoped lang="less">
.mine{
  background-color: #f2f2f2;
  .p1{
    display: inline-block;
    width: 100%;
    margin-top: 1.1rem;
    padding: .3rem;
    height: 1.4rem;
  }
  .top{
    background-color:white;
    a{
      border-bottom: .01rem solid #F5F5F5;
      display: inline-block;
      width: 100%;
      .left{
        padding:.3rem;
        font-size: .32rem;
      }
      .span1{
        display: inline-block;
        font-size: .3rem;
        color: #999;
        padding: .3rem 0 .3rem 4rem;
      }
      .right{
        font-size: .3rem;
        color: #999;
        margin: .3rem .1rem .3rem 0;
      }
    }
    .middle{
      border-bottom: .01rem solid #F5F5F5;
      .left{
        width: 15%;
        img{
          width: .8rem;
          height:.9rem;
          padding: .3rem;
        }
      }
      .right{
        width: 85%;
        padding: .3rem;
        h4{
          font-size: .3rem;
          font-weight: 400;
        }
        p{
          font-size: .2rem;
          color: #999;
        }
      }
    }
  }
  .bottom{
    background-color: white;
    margin-top: .25rem;
    h4{
      padding: .3rem;
      border-bottom: .01rem solid #F5F5F5;
    }
    span{
      display: inline-block;
      font-size: .3rem;
      padding: .45rem 0 .3rem .45rem;

      &:nth-child(2){
        font-weight: 700;
        color: #f60;
        padding-left: 0;
      }
    }
    button{
      border: .025rem solid #f60;
      border-radius: .1rem;
      line-height: .5rem;
      width: 1.2rem;
      text-align: center;
      font-size: .28rem;
      color: #f60;
      background-color: white;
      margin-left: 3.8rem;
    }
  }
  .card{
    margin-top: .3rem;
    background-color: white;
    .left{
      font-size: .34rem;
      padding: .3rem;
    }
    .right{
      display: inline-block;
      padding: .3rem 0 .3rem ;
      font-size: .3rem;
      color: #999;
    }
    .van1{
     padding: .3rem .1rem .3rem 0;
    }
  }
}
</style>



