<template>
    <div class="mine">
      <my-header></my-header>
      <div class="first">
        <div class="kuang">
          <span class="left left2">当前积分</span>
          <img src="data:image/png;base64," alt="">
          <router-link :to="{path:'/pointshuoming',query:{name:'积分问题'}}" class="left left2 ye">积分说明</router-link>
          <span class="ling ">0</span>
          <span>分</span>
          <button @click="shop">积分兑换商品</button>
        </div>
      </div>
      <!--交易明细-->
      <div class="second">
        <p class="p1">最近30天积分记录</p>
        <img src="data:image/png;base64," alt="">
        <p>最近30天无积分记录</p>
        <p class="p2">快去下单赚取大量积分吧</p>
      </div>
      <!--弹出的框加动画-->
      <transition enter-active-class="animated bounceIn" leave-active-class="opacity 0">
        <div class="bottom" v-if="show">
          <van-icon name="warning-o"  class="van1"/>
          <p>IOS用户请前往AppStore下载</p>
          <button @click="confirm">确认</button>
        </div>
      </transition>
    </div>
</template>

<script>

  import MyHeader from "./myHeader";
    export default {
        name: "point",
      components: {MyHeader},
      data(){
          return{
            show:false,
          }
      },
      methods:{
          shop(){
            this.show=true;
          },
          confirm(){
           this.show=false;
          }
      }
    }
</script>

<style scoped lang="less">
  .mine{
    width: 100%;
    hieght:80%;
    background-color: rgb(246,246,246);
  .first{
    display: inline-block;
    width: 100%;
    background-color: #3190e8;
    padding-bottom: .15rem;
    margin-top: .92rem;
  .kuang{
    width: 95%;
    background-color: #fff;
    margin: .2rem 2.5% 0;
    border-radius:.1rem;
    position: relative;
  img{
    width: .3rem;
    height: .3rem;
    position: absolute;
    right: 1.33rem;
    top: .2rem;
  }
  .left{
    width:50%;
  }
  .left2{
    font-size: .25rem;
    color: #666;
    padding: .25rem;
  }
  .ye{
    color: #3190e8;
    padding-left: 2.3rem;
  }
  .ling{
    font-size: .8rem;
    color: #333;
    padding-left: .2rem;
  }
  button{
    width: 95%;
    height: 1rem;
    font-size: .4rem;
    color: #fff;
    border-radius: .15rem;
    line-height: 1rem;
    text-align: center;
    background-color: #fe6d47;
    margin: .5rem 2.5% .2rem 2.5%;
  }

  }
  }
  .second{
    margin-top: .4rem;
  .p1{
    color: #999;
    font-size: .28rem;
    margin-left: .2rem;
  }
  img{
    display: inline-block;
    width: 4rem;
    height: 2.5rem;
    margin:1rem 0 0 2rem;
  }
  p{
    margin-top: .3rem;
    font-size: .33rem;
    color: #666;
    margin-left: 30%;
  }
    .p2{
      font-size: .22rem;
      margin-left: 30%;
    }
  }
    .bottom{
      width: 74%;
      background-color: #fff;
      text-align: center;
      position: absolute;
      left: 13%;
      top: 4rem;
      border-radius:.25rem .25rem 0 0 ;
      .van1{
        padding-top: .2rem;
        font-size: 1.5rem;
        color: #f8cb86;
      }
      p{
        font-size: .3rem;
        color: #333;
        line-height: .4rem;
        margin-top: .3rem;
        padding-bottom: .3rem;
      }
      button{
        font-size: .4rem;
        color: #fff;
        font-weight: 700;
        margin-top: .4rem;
        background-color: #4cd964;
        width: 100%;
        line-height: .9rem;
        border-radius: 0 0 .25rem .25rem;
      }
    }
  }

</style>



