<template>
    <div class="mine " @mousewheel.prevent
    >
      <my-header></my-header>
      <div class="first">
        <h3>Q1: 怎么获得红包？</h3>
        <ul>
          <li class="li1">通过邀请好友获得</li>
          <li class="li1">在下单返红包的商家下单获得</li>
          <li>抢微信朋友分享的红包获得</li>
        </ul>
        <h3>Q2: 红包可以做什么？</h3>
        <p>可以抵扣在线支付时的实际支付金额。</p>
        <h3>Q3: 红包的门槛金额按照什么金额计算？</h3>
        <p>红包的的门槛金额按照（选购菜品折后价＋餐盒费）的金额作为计算门槛，即购物车显示的金额（7.3以上版本）。假设红包满30可用，只要选购的菜品现价（折后价）＋餐盒费超过30，就可以使用红包。</p>
        <h3 class="q4">Q4: 一个红包能拆开多次使用吗？</h3>
        <p>不能，一个红包只能一次性使用，不能分开使用。</p>
        <!---->
        <h3 class="q4">Q5: 下单的时候使用了红包，但是后来订单取消了，红包还会返还吗？</h3>
        <p>会的，订单无效后红包会自动返还到您的账户里。</p>
        <h3 class="q4">Q6: 红包兑换码怎样兑换成红包，怎样查看红包？</h3>
        <p>在个人中心 > 我的红包 > 兑换红包，输入兑换码进行兑换。</p>
        <h3>Q7: 邀请好友了，为什么没获得红包？</h3>
        <p>先检查下您是否在同一设备上进行的邀请？或者被邀请人是否通过您发给对方的链接进行注册？而非自己复制链接注册的。同一设备上邀请或没有按照点击发送的链接邀请都是无效邀请。如果您是正常邀请没获得红包，可以致电客服进行查实。</p>
        <h3 class="q4">Q8: 预订早餐、预订午餐的红包怎么使用？在哪里查询？</h3>
        <ul>
          <li>预订早餐、预订午餐的红包暂时不支持直接点外卖使用，并且当前点外卖的红包也暂时不支持在预订早餐、预订午餐时使用。</li>
          <li>预订早餐、预订午餐的红包从APP首页点击“预订早餐”按钮进入后在右上角的个人中心中查找预订早餐、预订午餐的红包。</li>
        </ul>
      </div>
    </div>

</template>

<script>
  import MyHeader from "./myHeader";
    export default {
        name: "redpackge",
      components:{MyHeader}
    }
</script>

<style scoped lang="less">
  .mine{
    animation: fadeInRight 0.35s linear forwards;
    background-color: white;
  .first{
    width: 100%;
    display: inline-block;
    margin-top: 1.3rem;
    padding-left: .3rem;
  h3{
    color: #333;
    font-weight: 400;
    font-size: .33rem;
    box-sizing: border-box;
    margin-bottom: .2rem;
  }
  .q4{
    line-height: .8rem;
  }
  p,li{
    font-size: .28rem;
    color: #666;
    margin-bottom:.3rem ;
    line-height: .5rem;
  }
  .li1{
    margin-bottom: 0;
  }
  }
  }
</style>



