<template>
  <div class="wind">
    <div class="header">
      <img :src="'http://elm.cangdu.org/img/'+ shopinfor.image_path" class="urlimg">
      <!--<img src="//elm.cangdu.org/img/164ad0b6a3917599.jpg" alt="">-->
      <i class="iconfont" @click="goback">&#xe609;</i>
      <span class="s1">{{shopinfor.name}}</span>
      <span class="s2">商家配送 / 分钟到达 / {{shopinfor.piecewise_agent_fee.tips}}</span>
      <span class="s3">公告：欢迎光临，用餐高峰请提前下单，谢谢</span>
      <!--右箭头-->
      <i class="iconfont" style="font-size: 0.5rem;position: absolute;left: 6.8rem;top: 0.5rem;">&#xe60a;</i>
    </div>
    <!--中间部分-->
    <div class="center">
      <router-link :to="{path:'/shopingcar'}" class="commodity">商品</router-link>
      <!--<p class="xiahuaxian"></p>-->
      <router-link :to="{path:'/evaluate'}" class="evaluate">评价</router-link>
    </div>
    <!--下部左侧部分-->
    <div class="left">
      <ul v-for="(v,i) in array" @click="addunit(v)">
        <!--<li>-->
        <li>{{v.name}}</li>
        <!--{{v.name}}-->
        <!--<p class="zuohuaxian"></p>-->
        <!--</li>-->
        <!--<li><span>热搜榜</span></li>-->
      </ul>
    </div>
    <!--下部右侧部分-->
    <div class="right" v-for="value in arr">
      <ul>
        <!--下部右侧部分头部-->
        <header style="margin-top: 0.01rem;padding-bottom: 0.25rem">
          <span class="rexiao">{{value.name}}</span>
          <span class="shide">{{value.description}}</span>
          <span class="shenglue">...</span>
        </header>
        <li>
          <!--下部右侧部分第一块-->
          <section class="center_one" v-for="v in value.foods" @click="toask">
            <!--短信图标-->
            <!--<i class="iconfont" style="color: rgb(18,150,219);margin-top: 4.4rem;margin-left:2rem;font-size:0.9rem">&#xe827;</i>-->
            <img :src="'//elm.cangdu.org/img/'+v.image_path" class="ac" alt="">
            <div class="abc">
              <p class="wen">{{v.name}}</p>
              <p class="fa">{{v.description}}</p>
              <p class="yue">{{v.tips}}好评率85%</p>
              <p class="de">的</p>
              <!--价格容器-->
              <p>￥<span
                style="color:#f60;font-size: 0.4rem">{{v.specfoods[0].price}}</span>
                <span style="color: #666;font-size: 0.2rem;">起</span>
                <span @touchstart.stop.prevent="btnjian(v)" class="btnjian" v-if="v.specfoods[0].num>0"><van-icon
                  name="add-o"/></span>
                <span @touchstart.stop.prevent="btnAn(v)" class="btnAn">{{v.specfoods[0].num}}<van-icon
                  name="add-o"/></span>
              </p>
            </div>
          </section>
          <!--下部右侧部分第二块-->
          <!--<section class="center_Two">-->
          <!--&lt;!&ndash;左上角新品字样&ndash;&gt;-->
          <!--<i class="zuoshang">-->
          <!--<p>新品</p>-->
          <!--</i>-->
          <!--&lt;!&ndash;图片&ndash;&gt;-->
          <!--<img class="center_img" src="../pages/img/31.jpg">-->
          <!--<p class="tui">腿aaa</p>-->
          <!--<p class="shuzi">5201314666</p>-->
          <!--<p class="shou">月售29份 好评率66%</p>-->
          <!--<p style="color: #f60;font-size: 0.25rem;margin-left: 1.2rem;margin-top: 0.3rem">￥<span style="color:#f60;font-size: 0.4rem">20</span>-->
          <!--<span style="color: #666;font-size: 0.2rem;">起</span>-->
          <!--<span class="btnAn">选规格</span>-->
          <!--</p>-->
          <!--</section>-->
        </li>
      </ul>
    </div>
    <div class="bottom">
      <!--底部购物车-->
      <section class="shopping">
        <!--购物车图标-->
        <i class="iconfont"
           style="font-size: 1rem;border: 1px solid #444;border-radius: 50%;margin-left: 0.3rem;margin-top: -0.4rem;background-color: rgb(61,61,63);">&#xe605;</i>
        <!--购物车配送费 总价容器-->
        <div class="bottom_money">
          <div class="jinbi">￥ 0.00</div>
          <div class="peisong">配送费￥5</div>
        </div>
        <!--还差多少元起送按钮-->
        <div class="chajia">
          <span>还差￥20起送</span>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
  import Vue from "vue"

  export default {
    name: "shopingcar",
    data() {
      return {
        array: [],
        arr: [],
        shopinfor: "",//店铺信息
        orderlistinfor: "",//订单列表信息
      }
    },
    methods: {
      btnAn(v) {
        if (!v.specfoods[0].num) {
          this.$set(v.specfoods[0], "num", 1)
        } else {
          this.$set(v.specfoods[0], "num", v.specfoods[0].num + 1)
        }


        console.log(v);
      },
      btnjian(v) {
        this.$set(v.specfoods[0], "num", v.specfoods[0].num - 1)

      },
      goback() {
        this.$router.go(-1)
      },
      addunit(value) {
        // if(value.name=="热销榜"){
        //   this.arr=this.array
        // }
        this.arr = this.array.filter((pro) => {
          return pro.name === value.name;
        })
        // console.log(this.arr);
      },
      toask() {
        this.$router.push({path: "/ask"})
      }
    },
    created() {
      this.shopinfor = this.$store.state.shopinfor
      this.orderlistinfor = this.$route.query.reid
      // = this.$store.state.orderlistinfor.restaurant_id

    },
    mounted() {
      Vue.axios.get("/shopping/v2/menu", {
        params: {
          restaurant_id: this.orderlistinfor
        }
      }).then((result) => {
        console.log(result.data);
        this.array = result.data;
        console.log(this.array);
        this.$store.commit("saveallgoodsinfor", this.array)
        this.addunit({name: '热销榜', description: '是的分身乏术'})
        // console.log(this)
      }).catch((error) => {
        console.log(error)
      })
    }
  }
</script>

