<template>
    <div class="mine">
      <my-header></my-header>
      <div class="first">
        <h2>{{gettxt}}</h2>
      </div>


    </div>
</template>

<script>
  import MyHeader from "./myHeader";
    export default {
        name: "questionDetail",
      components:{MyHeader},
      computed:{
        gettxt(){
          return this.$route.query.txt
        }
      }
    }
</script>

<style scoped lang="less">
.first{
  margin-top: 1rem;
  padding: .2rem;
}
</style>



