<template>
    <div class="mine">
      <my-header></my-header>
      <div class="top">
        <p>成功兑换后将关联到当前账号:
        <span>{{username}}</span>
        </p>
      </div>
      <!--输入框-->
      <input type="number" placeholder="请输入10位卡号" oninput="if(value.length>10)value=value.slice(0,10)" v-model="input1">
      <input type="number" placeholder="请输入6位卡密" oninput="if(value.length>6)value=value.slice(0,6)" v-model="input2">
      <!--button按钮-->
      <button class="btn1" @click="btn" :style="{'background':bgc}" :disabled="disabled">兑换</button>
      <!--底部提示-->
      <div class="bottom">
        <h4>——温馨提示——</h4>
        <p>新兑换的会员服务，权益以「会员说明」为准。</p>
        <p>月卡： 30次减免配送费</p>
        <p>季卡： 90次减免配送费</p>
        <p>年卡： 360次减免配送费</p>
        <p>＊仅限蜂鸟专送订单，每日最多减免3单，每单最高减免4元（一个月按31天计算）</p>
      </div>

      <!--点击兑换弹框-->
      <transition enter-active-class="animated bounceIn " leave-active-class="opacity 0">
        <div class="kuang" v-if="show">
          <van-icon name="warning-o"  class="van1"/>
          <p>无效的卡号</p>
          <button @click="confirm">确认</button>
        </div>
      </transition>

    </div>
</template>

<script>
  import MyHeader from "./myHeader";
    export default {
      name: "exchangemembers",
      components: {MyHeader},
      data() {
        return {
          show: false,
          username: "",
          input1: "",
          input2: "",
          bgc: "#ccc",
          isinput1: "",
          isinput2: "",
          disabled: true,
        }
      },
      methods: {
        btn() {
          this.show = true;
        },
        confirm() {
          this.show = false;
        }
      },

      watch: {
        input1() {
          if (this.input1.length == 10) {
            this.isinput1 = true;
          }else{
            this.isinput1 = false;
          }
          if (this.isinput1 && this.isinput2) {
            this.bgc = "#4cd964";
            this.disabled = false;
          }
        },

        input2() {
          if (this.input2.length == 6) {
            this.isinput2 = true;
          }else{
            this.isinput2 = false;
          }
          if (this.isinput1 && this.isinput2) {
            this.bgc = "#4cd964";
            this.disabled = false;
          }
        },
      },

      created(){
        //  把vuex中的用户名信息取出来赋值给this.username
        let localData = this.$store.state.userStatus;
        this.username=localData.username;
        console.log(this.username);
      }
    }
</script>

<style scoped lang="less">
.mine{
  background-color: #f2f2f2;
  .top{
    width: 100%;
    display: inline-block;
    margin-top: 1.1rem;
    p{
      width: 100%;
      line-height: .8rem;
      box-sizing: border-box;
      padding: 0 .5rem .1rem .3rem;
      color: #666;
      font-size: .3rem;
      span{
        box-sizing: border-box;
        color: black;
      }
    }
  }
 input{
   background-color: white;
   line-height: .9rem;
   padding-left: .2rem;
   display: block;
   width: 100%;
   border-bottom: .01rem solid #f5f5f5;
 }
  .btn1{
    background-color: #ccc;
    font-size: .33rem;
    color: #fff;
    width: 94%;
    margin-left: 3%;
    text-align: center;
    line-height: .9rem;
    border-radius: .1rem;
    margin-top: .4rem;
    font-weight: 700;
  }
  .bottom{
    text-align: center;
    margin-top: .4rem;
    h4{
      color: #aaa;
      margin-bottom: .3rem;
    }
    p{
      text-align: left;
      font-size: .2rem;
      color: #aaa;
      padding-top: .15rem;
      margin-left: 1rem;
    }
  }
  .kuang{
    width: 74%;
    background-color: #fff;
    text-align: center;
    position: absolute;
    left: 13%;
    top: 3rem;
    border-radius:.25rem .25rem 0 0 ;
    .van1{
      padding-top: .2rem;
      font-size: 1.5rem;
      color: #f8cb86;
    }
    p{
      font-size: .3rem;
      color: #333;
      line-height: .4rem;
      margin-top: .3rem;
      padding-bottom: .3rem;
    }
    button{
      font-size: .4rem;
      color: #fff;
      font-weight: 700;
      margin-top: .4rem;
      background-color: #4cd964;
      width: 100%;
      line-height: .9rem;
      border-radius: 0 0 .25rem .25rem;
    }
  }
}
</style>



