<template>
  <div>
    <childheader v-bind:storename="shopname"></childheader>
    <!--轮播图-->
    <van-swipe class="white">
      <van-swipe-item class="swite">
        <div>
          <ul>
            <li class="lunboli" v-for="a in array2" @click="inlunbopage(a)">
              <img :src="'https://fuss10.elemecdn.com'+a.image_url">
              <br/>
              <span> {{a.title}}</span>
            </li>
          </ul>
        </div>
      </van-swipe-item>
      <van-swipe-item class="swite">
        <div>
          <ul>
            <li class="lunboli" v-for="a in array1" @click="inlunbopage(a)">
              <img :src="`https://fuss10.elemecdn.com/${a.image_url}`">
              <span>{{a.title}}</span>
            </li>
          </ul>
        </div>
      </van-swipe-item>
    </van-swipe>


    <div class="Armin"></div>
    <div class="bott">
      <!--表头-->
      <div class="top">
        <i class="iconfont">&#xe608;</i>
        <span>附近商家</span>
      </div>
      <!--、商品信息-->
      <div class="center clearfix">
        <!--商品循环列表-->
        <div v-for="(item,index) in rest" class="rest-a" @click="entershop(item)">
          <img :src="'http://elm.cangdu.org/img/'+ item.image_path" class="urlimg">
          <span class="pinpai">品牌</span>
          <span class="mingzi">{{item.name}}</span>
          <span class="baopiao right">保 准 票</span>
          <!--vant 星星-->
          <div class="clearfix" style="margin-top: 0.16rem">
            <van-rate :size="2"
                      value="5"
                      readonly
                      color="gold"
                      void-icon="star"
                      void-color="wheat" v-model="item.rating" class="vantRa left"/>
            <span class="pingfen left">{{item.rating}}</span>
            <span class="yueshou">月售{{item.recent_order_num}}单</span>
            <span class="fengniao">{{item.delivery_mode.text}}</span>
            <span class="zhunshida">准时达</span>
          </div>
          <div class="clearfix">
            <span class="peisong left">￥{{item.float_minimum_order_amount}}起送/配送费￥{{item.float_delivery_fee}}</span>
            <span class="gongli">{{item.distance}} /</span>
            <span class="fenzhong">{{item.order_lead_time}}</span>
          </div>
        </div>
      </div>
    </div>

    <childfooter v-bind:isres="msg"></childfooter>

  </div>
</template>

<script>
  import Vue from "vue"
  import Childheader from "./common/childheader";
  import Childfooter from "./common/childfooter";


  export default {
    name: "waimaipage",
    components: {Childfooter, Childheader},
    data() {
      return {
        array1: [],
        array2: [],
        rest: [],
        msg: '外卖',//底部按钮颜色
        shopname: "",//点击位置的商家名字
        shoplatitude: "",//当前店家的纬度信息
        shoplongitude: "",//当前店家的经度信息
        geohash: "",//商店经纬度信息

      }
    },
    created() {
      localStorage.tomsg = '外卖'
      this.getlunbodata()

      //从vuex中取数据latitude
      let vuexlatitude = this.$store.state.lacalshoplatitude;
      this.shoplatitude = vuexlatitude

      //从vuex中取数据longitude
      let vuexlongitude = this.$store.state.lacalshoplongitude;
      this.shoplongitude = vuexlongitude

      //从vuex中取数据geohash
      let vuexgeohash = this.$store.state.localgeohash;
      this.geohash = vuexgeohash


      Vue.axios.get(`/v2/pois/${this.geohash}`).then((res) => {
        this.shopname = res.data.name
        this.shoplatitude = res.data.latitude
        this.shoplongitude = res.data.longitude
      }).catch((error) => {
        console.log(error);
      })


    },
    mounted() {
      this.getgoodsdata()
    },
    methods: {
      //进入选中商家店铺
      entershop(v) {
        this.$store.commit('saveshopinfor',v)
        this.$router.push({path:"/shopingcar",query:{
          reid:v.id,
          }})
      },

      //获取轮播图数据数据
      getlunbodata() {
        Vue.axios.get("/v2/index_entry").then((result) => {
          // 第二页
          this.array2 = result.data.splice(0, 8);
          // 第一页
          this.array1 = result.data;
        });
      },
      //点击轮播模块进入商品分类页面
      inlunbopage(v) {
        // 轮播图分类名字传入vuex中
        this.$store.commit('saveloaclname', v.title)

        this.$router.push({path: "/lunboenterpage"})

      },


      // 获取商品信息
      getgoodsdata() {
        Vue.axios.get("/shopping/restaurants", {
          params: {
            latitude: this.shoplatitude,
            longitude: this.shoplongitude,
          }
        }).then((resault) => {
          console.log(resault.data[1]);
          this.rest = resault.data
          console.log(this.rest);
        }).catch((error) => {
          console.log(error)
        })
      }
    }
  }
</script>

<style scoped>
  li {
    width: 25%;
    float: left;
    text-align: center;
  }

  .rest-a {
    height: 2rem;
    border-bottom: 1px solid #e4e4e4;
    padding-bottom: 2.2rem;
  }

  img {
    display: inline-block;
    width: 60%;

  }

  .white {
    padding-bottom: 0.5rem;
    margin-top: 1.2rem;
  }

  .lunboli {
    padding: 0.2rem;
    font-size: 0.25rem;
  }

  .lunboli span {
    display: inline-block;
    margin-top: 0.2rem;
  }

  .Armin {
    width: 100%;
    height: 0.2rem;
    background-color: rgb(238, 238, 238);
    margin-top: 0.1rem;
  }

  .bott {
    width: 100%;
  }

  .top {
    width: 100%;
    margin-top: 0.2rem;
  }

  .top i {
    margin-left: 0.3rem;
    color: #afafaf;
    /*调整基线*/
    vertical-align: -0.04rem;
  }

  .top span {
    display: inline-block;
    color: #afafaf;
  }

  .iconfont {
    font-size: 0.4rem;
    margin-left: 0.5rem;
  }

  .center {
    width: 100%;
    font-size: 0;
    display: inline-block;
  }

  .baopiao {
    margin-top: 0;
  }

  .urlimg {
    width: 1.5rem;
    height: 1.5rem;
    padding-top: 0.5rem;
    padding-left: 0.15rem;
  }

  .pinpai {
    font-size: 0.25rem;
    border: 0.01rem solid rgb(255, 217, 58);
    background-color: rgb(255, 217, 58);
    border-radius: 0.03rem;
    margin: 0 0.1rem 0 0.2rem;
    font-weight: bolder;
    vertical-align: 1.24rem;
  }

  .mingzi {
    display: inline-block;
    font-weight: bolder;
    vertical-align: 1.2rem;
    font-size: 0.3rem;
    width: 3.4rem;
    /*超出文字以省略号表示*/
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  .baopiao {
    font-size: 0.17rem;
    margin-top: 0.55rem;
    margin-right: 0.2rem;
  }

  .vantRa {
    margin-top: -1rem;
    margin-left: 1.85rem;
  }

  .pingfen {
    color: red;
    font-size: 0.2rem;
    margin-left: 3.3rem;
    margin-top: -1rem;
    background-color: yellow;
  }

  .yueshou {
    font-size: 0.22rem;
    float: left;
    margin-left: 3.7rem;
    margin-top: -0.98rem;
  }

  .fengniao {
    font-size: 0.11rem;
    background-color: rgb(49, 144, 232);
    color: white;
    float: left;
    padding: 0.03rem;
    margin-left: 5.74rem;
    margin-top: -1.04rem;
  }

  .zhunshida {
    font-size: 0.12rem;
    border: 1px solid rgb(49, 144, 232);
    text-align: center;
    border-radius: 0.05rem;
    color: rgb(49, 144, 232);
    float: right;
    margin-top: -1.02rem;
    margin-right: 0.22rem;
  }

  .peisong {
    color: gray;
    font-size: 0.1rem;
    margin-left: 1.8rem;
    margin-top: -0.5rem;
  }

  .gongli {
    float: right;
    font-size: 0.1rem;
    margin-right: 1.4rem;
    margin-top: -0.5rem;
  }

  .fenzhong {
    float: left;
    font-size: 0.1rem;
    margin-left: 6.2rem;
    margin-top: -0.5rem;
    color: rgb(49, 144, 232);
  }
</style>



