<template>
    <div class="mine">
      <my-header></my-header>
      <div class="first">
        <h3>Q1: 什么是商家代金券？</h3>
       <p>商家代金券是指由商家自己发放代金券，只限在指定的商家使用，可根据条件抵扣相应金额。</p>
        <h3>Q2: 怎么获得商家代金券？</h3>
        <ul>
          <li  class="li1">进入有「进店领券」或「下单返券」标示的商家即有机会获得代金券。</li>
          <li>「下单返券」需要在指定商家完成满足返券金额要求的订单后才会返还，代金券可在下次下单时使用。</li>
        </ul>

        <h3>Q3: 商家代金券使用条件</h3>
        <ul>
          <li class="li1">商家代金券仅限在指定商家使用</li>
          <li class="li1">商家代金券仅限在线支付使用</li>
          <li>每个订单只能使用一张商家代金券，不可与其他代金券叠加使用</li>
        </ul>

      </div>
    </div>
</template>
//      代金券说明
<script>

  import MyHeader from "./myHeader";
    export default {
        name: "djqsm",
      components: {MyHeader},
    }
</script>

<style scoped lang="less">
  .mine{
    animation: fadeInRight 0.35s linear forwards;
    height: 100%;
    background-color: white;
  .first{
    width: 100%;
    display: inline-block;
    margin-top: 1.3rem;
    padding-left: .3rem;
  h3{
    color: #333;
    font-weight: 400;
    font-size: .33rem;
    box-sizing: border-box;
    margin-bottom: .2rem;
  }
  p,li{
    font-size: .28rem;
    color: #666;
    margin-bottom:.3rem ;
    line-height: .5rem;
  }
  .li1{
    margin-bottom: 0;
  }
  }
  }
</style>



