import Vue from 'vue'
import Router from 'vue-router'
// 页面dome
//登录页面
import loginpage from "../pages/loginpage"
// 首页
import index from "../pages/index"
// 城市页面
import citypage from "../pages/citypage"
// 忘记密码页面
import forgetpage from "../pages/forgetpage"
// 搜索页面
import searchpage from "../pages/searchpage"
// 订单页面
import orderpage from "../pages/orderpage"
//外卖页面
import waimaipage from "../pages/goodspage/waimaipage"
//轮播图分类页面
import lunboenterpage from "../pages/goodspage/lunboenterpage"
//购物车模块
import shopingcar from "../pages/shopingcar"
import evaluate from "../pages/goodspage/evaluate"
// 丽华的模块引入
import cheap from "../pages/mine-2/cheap"
import yue from "../pages/mine-2/yue"
import money from "../pages/mine-2/money"
import profilepage from "../pages/profilepage"
import point from "../pages/mine-2/point"
import pointshuoming from "../pages/mine-2/pointshuoming"
import redpackge from "../pages/mine-2/redpackge"
import daijinquan from "../pages/mine-2/daijinquan"
import money1 from "../pages/mine-2/money-1"
import redpackgehistory from "../pages/mine-2/redpackgehistory"
import djqsm from "../pages/mine-2/djqsm"
import loadelm from "../pages/mine-2/loadelm"
import account from "../pages/mine-2/account"
import service from "../pages/mine-2/service"
import questionDetail from "../pages/mine-2/questionDetail"
import changeusername from "../pages/mine-2/changeusername"
import address from "../pages/mine-2/address"
import changepassword from "../pages/mine-2/changepassword"
import addaddress from "../pages/mine-2/addAddress"
import searchaddress from "../pages/mine-2/searchaddress"
import card from "../pages/mine-2/card"
import maiyouhui from "../pages/mine-2/mineyouhui"
import shophistory from "../pages/mine-2/shophistory"
import exchangemembers from "../pages/mine-2/exchangemembers"
import pay from "../pages/mine-2/pay"
import prize from "../pages/mine-2/prize"
import redpacket from "../pages/mine-2/redpacket"
import ask from "../pages/orderpage-2/ask"
import confirmorder from "../pages/orderpage-2/confirmorder"
import chooseaddress from "../pages/orderpage-2/chooseaddress"
import newaddress from "../pages/orderpage-2/newaddress"


// import header from "header"
// import footer from "footer"
// import citypage from "citypage"
// import orderpage from "orderpage"
// import profilepage from "profilepage"
// import searchpage from "searchpage"
// import waimaipage from "waimaipage"


Vue.use(Router)

export default new Router({
  linkActiveClass: 'is-active',
  routes: [
    // 首页
    {path: "/", redirect: "/index"},
    {path: "/index", component: index},
    // 登录页面
    {path: "/loginpage", component: loginpage},
    // 城市页面
    {path: "/citypage", component: citypage},
    // 忘记面页面
    {path: "/forgetpage", component: forgetpage},
    // 订单页面
    {path: "/orderpage", component: orderpage},
    // 搜索页面
    {path: '/searchpage', component: searchpage},
    //外卖页面
    {path: '/waimaipage', component: waimaipage},
    //外卖进入页面
    {path: "/lunboenterpage", component: lunboenterpage},
    //购物车路由
    {path: "/shopingcar", component: shopingcar},
    {path: "/evaluate", component: evaluate},

    // {path:"/",required:"/home"},
    // {path:"/home",required:home},

    // 丽华的路由配置
    {path: "/profilepage", component: profilepage},
    {path: "/money", component: money},
    {
      path: "/cheap", component: cheap, children: [
        {path: "", redirect: "money1"},
        {path: "money1", component: money1},
        {path: "daijinquan", component: daijinquan},
      ]
    },
    {path: "/yue", component: yue},
    {path: "/point", component: point},
    {path: "/pointshuoming", component: pointshuoming},
    {path: "/redpackge", component: redpackge},
    {path: "/redpackgehistory", component: redpackgehistory},
    {path: "/djqsm", component: djqsm},
    {path: "/loadelm", component: loadelm},
    {path: "/account", component: account},
    {path: "/service", component: service},
    {path: "/questionDetail", component: questionDetail},
    {path: "/changeusername", component: changeusername},
    {path: "/address", component: address},
    {path: "/changepassword", component: changepassword},
    {path: "/addaddress", component: addaddress},
    {path: "/searchaddress", component: searchaddress},
    {path: "/card", component: card},
    {path: "/mineyouhui", component: maiyouhui},
    {path: "/shophistory", component: shophistory},
    {path: "/exchangemembers", component: exchangemembers},
    {path: "/pay", component: pay},
    {path: "/prize", component: prize},
    {path: "/redpacket", component: redpacket},
    {path: "/ask", component: ask},
    {path: "/confirmorder", component: confirmorder},
    {path: "/chooseaddress", component: chooseaddress},
    {path: "/newaddress", component: newaddress},


  ]
})



