<template>
    <div class="mine">
      <my-header></my-header>
     <div class="first">
       <div class="kuang">
         <span class="left left2">当前余额</span>
         <img src="data:image/png;base64," alt="">
         <router-link :to="{path:'/yue',query:{name:'余额问题'}}" class="left left2 ye">余额说明</router-link>
         <span class="ling ">0.00</span>
         <span>元</span>
         <button>提现</button>
       </div>
     </div>
      <!--交易明细-->
     <div class="second">
       <p class="p1">交易明细</p>
       <img src="data:image/png;base64," alt="">
       <p>暂无明细记录</p>
     </div>
    </div>
</template>

<script>

    import MyHeader from "./myHeader";
    export default {
        name: "money",
      components: {MyHeader},
    }
</script>

<style scoped lang="less">
.mine{
  width: 100%;
  hieght:80%;
  background-color: rgb(246,246,246);
  .first{
    display: inline-block;
    width: 100%;
    background-color: #3190e8;
    padding-bottom: .15rem;
    margin-top: .92rem;
    .kuang{
      width: 95%;
      background-color: #fff;
      margin: .2rem 2.5% 0;
      border-radius:.1rem;
      position: relative;
      img{
        width: .3rem;
        height: .3rem;
        position: absolute;
        right: 1.33rem;
        top: .2rem;
      }
      .left{
        width:50%;
      }
      .left2{
        font-size: .25rem;
        color: #666;
        padding: .25rem;
      }
      .ye{
        color: #3190e8;
        padding-left: 2.3rem;
      }
      .ling{
        font-size: .8rem;
        color: #333;
        padding-left: .2rem;
      }
      button{
        width: 95%;
        height: 1rem;
        font-size: .4rem;
        color: #fff;
        border-radius: .15rem;
        line-height: 1rem;
        text-align: center;
        background-color: #ccc;
        margin: .5rem 2.5% .2rem 2.5%;
      }

    }
    }
  .second{
    margin-top: .4rem;
    .p1{
      color: #999;
      font-size: .28rem;
      margin-left: .2rem;
    }
    img{
      display: inline-block;
      width: 4rem;
      height: 2.5rem;
      margin:.4rem 0 0 2rem;
    }
    p{
      margin-top: .3rem;
      font-size: .33rem;
      color: #666;
      margin-left: 36%;
    }
  }
  }

</style>



